<template>
    <div class="School_box"  @scroll="Scroll">
        <div class="S_head">
            <img src="@/assets/img_30.png" :class="['menu',menuShow?'active':'']" @click="Change(0)" alt="">
            <div :class="['type',typeShow?'active':'']" @click="Change(1)">
                <span>{{tab_list[active]}}</span>
                <img src="@/assets/img_31.png" alt="">
            </div>
            <img src="@/assets/img_29.png" class="search" @click="Search" alt="">
        </div>

        <div class="Cover_1" v-show="typeShow||menuShow"></div>
        <div :class="['type_list',typeShow?'active':'']">
            <div :class="['type_item',active===index?'active':'']" v-for="(el,index) in tab_list" :key="index" @click="tabChange(index)">
                {{el}}
            </div>
        </div>

        <div class="List_box_o" v-if="dataList.length>0">
            <div class="V_item_o" v-for="(el,index) in dataList" :key="index" @click="Detail(el)">
                <div class="I_box">
                    <img :src="el.image" class="m_img" alt="">
                </div>
                <div class="content">
                    <h1 class="title">
                        <span class="im" v-if="el.is_important_msg=='1'">
                            [重要]
                        </span>
                        {{el.title}}
                    </h1>
                    <h2>
                        分类：
                        <label v-if="!!el.first&&!!el.first.name">{{el.first.name}}</label>
                        <label v-if="!!el.second&&!!el.second.name">-{{el.second.name}}</label>
                        <label v-if="!!el.third&&!!el.third.name">-{{el.third.name}}</label>
                    </h2>
                    <div class="time" @click="ChangeZan(index)">
                        <span>{{ !!el.timing_date?TimeChangeA(el.timing_date):el.created_at}}</span>
                        <img v-if="el.like===null" src="@/assets/img_37.png" alt="" @click.stop="ChangeLike(el);">
                        <img v-else src="@/assets/img_38.png" alt=""  @click.stop="ChangeLike(el)">
                        <label>{{el.like_number}}</label>
                    </div>
                </div>
            </div>
        </div>
        <NoData v-else />
        

        <div :class="['Menu_list',menuShow?'active':'']">
            <Menu v-for="(el,index) in menuList" :key="index" :menuInfo='el' :level='0' :active.sync='menuActive' @ChangeActive='ChangeActive' />
        </div>
    </div>
</template>

<script>
    import Menu from './components/menu'
    export default {
        components:{Menu},
        data(){
            return{
                active:0,
                tab_list:['全部内容','已阅读','未阅读'],
                dataList:[],
                statusList:['未开始','进行中','已结束'],
                class_:['S_1','S_2','S_3'],
                typeShow:false,
                menuShow:false,
                menuActive:'all',
                menuList:[
                ],
                pageSize:10,
                pageNumber:1,
                total:0,
                oldScrollTop:0,
                canGet:true,
                level:0,
            }
        },
        created() {
            this.getClass();
            this.GetList();
        },
        watch: {
            menuActive:function(v){
                this.ReGet();
            },
        },
        methods: {
            Search(){
                this.$router.push({path:'/search'});
            },
            tabChange(index){
                this.active = index;
                this.typeShow = false;
                this.ReGet();
            },
            ReGet(){
                this.dataList = [];
                this.pageNumber = 1;
                this.GetList();
            },
            Detail(item){
                this.$store.commit('setValue',{key:'schoolInfo',value:item});
                this.$router.push({path:'/school/detail'});
            },
            ChangeZan(index){
                if(this.dataList[index].zan){
                    this.dataList[index].count--;
                }else{
                    this.dataList[index].count++;
                }
                this.dataList[index].zan = !this.dataList[index].zan;
            },
            Change(type){
                if(type === 0){
                    this.menuShow = !this.menuShow;
                    this.typeShow = false;
                }
                if(type === 1){
                    this.typeShow = !this.typeShow;
                    this.menuShow = false;
                }
            },
            ChangeActive(id,level){
                console.log(id,level);
                
                this.level = level + 1;
                this.menuActive = id;
            },
            async getClass(){
                let class_ = await this.Ajax.ClassCategory({type:2});
                this.menuList = class_.data;
            },
            async GetList(){
                let info = {
                    page:this.pageNumber,
                    type:2,
                    keywords:'',
                    is_read:'',
                    category_id:'',
                }
                if(this.menuActive!='all'){
                    info.category_id = this.menuActive;
                    info.level = this.level;
                }
                let readType = ['','1','0'];
                info.is_read = readType[this.active];
                let data = await this.Ajax.MessageList(info);
                let list = data.data.data.map(el=>{
                    el.created_at = window.GetTimeType(parseInt(el.created_at*1000),'yyyy.MM.dd hh:mm:ss');
                    el.image = JSON.parse(el.image);
                    if(!!el.image&&el.image.length>0){
                        el.image = el.image[0];
                    }else{
                        el.image = require('@/assets/img_56.png');
                    }
                    return el;
                })
                this.total = parseInt(data.data.total);
                this.dataList = this.dataList.concat(list);
                this.$nextTick(()=>{
                    this.canGet = true;
                })
            },
            Scroll(event){
                let direction = '';
                if(this.oldScrollTop > event.target.scrollTop){
                    direction = 'up';
                }else{
                    direction = 'down';
                }
                this.oldScrollTop = event.target.scrollTop;
                let scrollBottom = event.target.scrollHeight - event.target.scrollTop - event.target.clientHeight;
                if(direction === 'down' && scrollBottom < 100 && this.canGet && this.total > (this.pageNumber*this.pageSize)){
                    this.canGet = false;
                    this.pageNumber++;
                    this.GetList();
                }
            },
            async ChangeLike(el){
                if(!!el.like){
                    let CancelLike = await this.Ajax.MessageCancelLike({article_id:el.id});
                }else{
                    let like = await this.Ajax.MessageLike({id:el.id});
                    console.log(like);
                }
                this.ReGet();
            },
        },
    }
</script>

