<template>
    <div class="M_box"  @scroll="Scroll">
        <div class="tabs_">
            <span v-for='(el,index) in tab_list' :key="index" :class="el.key===active?'active':''" @click="tabChange(el)">
                {{el.name}}
            </span>
        </div>
        <div v-if="dataList.length>0">
            <ActivityItem v-for="(el,index) in dataList" :key="index" :Item='el' @Detail='Detail' />
        </div>
        <NoData v-else />
    </div>
</template>

<script>
    export default {
        data() {
            return {
                tab_list:[
                    {
                        name:'全部',
                        key:'',
                    },
                    {
                        name:'未开始',
                        key:'10',
                    },
                    {
                        name:'进行中',
                        key:'20',
                    },
                    {
                        name:'已结束',
                        key:'30',
                    },
                ],
                active:'',
                pageSize:10,
                pageNumber:1,
                total:0,
                dataList:[],
                oldScrollTop:0,
                canGet:true,
            }
        },
        created() {
            this.ReGet();
        },
        methods: {
            tabChange(el){
                this.active = el.key;
                this.ReGet();
            },
            ReGet(){
                this.total = 0;
                this.pageNumber = 1;
                this.dataList = [];
                this.GetList();
            },
            Detail(el){
                this.$store.commit('setValue',{key:'activityInfo',value:el});
                this.$router.push({path:'/activity/detail'});
            },
            async GetList(){
                let info = {
                    page:this.pageNumber,
                    join:1,
                    status:this.active,
                }
                let data = await this.Ajax.ActivityList(info);
                let list = data.data.data.map(el=>{
                    el.startTime = window.GetTimeType(parseInt(el.start_time*1000),'yyyy.MM.dd hh:mm:ss');
                    el.endTime = window.GetTimeType(parseInt(el.end_time*1000),'yyyy.MM.dd hh:mm:ss');
                    el.image = JSON.parse(el.image);
                    if(!!el.image&&el.image.length>0){
                        el.image = el.image[0];
                    }else{
                        el.image = require('@/assets/img_14.png');
                    }
                    return el;
                })
                this.total = parseInt(data.data.total);
                this.dataList = this.dataList.concat(list);
                this.$nextTick(()=>{
                    this.canGet = true;
                })
            },
            Scroll(event){
                let direction = '';
                if(this.oldScrollTop > event.target.scrollTop){
                    direction = 'up';
                }else{
                    direction = 'down';
                }
                this.oldScrollTop = event.target.scrollTop;
                let scrollBottom = event.target.scrollHeight - event.target.scrollTop - event.target.clientHeight;
                if(direction === 'down' && scrollBottom < 100 && this.canGet && this.total > (this.pageNumber*this.pageSize)){
                    this.canGet = false;
                    this.pageNumber++;
                    this.GetList();
                }
            },
        },
    }
</script>

