<template>
    <div class="Content_box" style="padding-bottom:0;" @scroll="Scroll">
        <div class='Home_head'>
            <div class="Search_box">
                <div class="box">
                    <input type="text" v-model="keywords" placeholder="请输入内容">
                    <img src="@/assets/img_15.png" alt="" @click="ReGet">
                </div>
                <img src="@/assets/img_17.png" class="date" @click="dataShow=true;" alt="">
            </div>
            <div class="tabs_">
                <span v-for='(el,index) in tab_list' :key="index" :class="index===active?'active':''" @click="tabChange(index)">
                    {{el}}
                </span>
            </div>
        </div>

        <div class="List_box" v-if="dataList.length>0">
            <ExamItem v-for="(el,index) in dataList" :Item='el' :key="index" @Detail='Detail' />
        </div>
        <NoData v-else />

        <DateItem :show.sync='dataShow' @Comfirm='DateComfirm' />
    </div>
</template>

<script>
    import DateItem from '@/components/Date/index'
    export default {
        components:{DateItem},
        data(){
            return{
                active:0,
                tab_list:['全部','待考试','已考完'],
                dataList:[],
                pageSize:10,
                pageNumber:1,
                total:0,
                dataShow:false,
                keywords:'',
                startTime:'',
                endTime:'',
                oldScrollTop:0,
                canGet:true,
            }
        },
        created() {
        },
        watch: {
            keywords:function(v){
                this.ReGet();
            },
        },
        methods: {
            tabChange(index){
                this.active = index;
                this.ReGet();
            },
            DateComfirm(time){
                this.dataShow = false;
                this.startTime = time.start;
                this.endTime = time.end;
                this.ReGet();
            },
            ReGet(){
                this.total = 0;
                this.pageNumber = 1;
                this.GetList('Re');
            },
            Detail(item){
                this.$store.commit('setValue',{key:'examInfo',value:item});
                if(item.status_ == 4 || item.status_ == 5 || item.status_ == 6){
                    this.$router.push({path:'/exam/result'});
                }else{
                    this.$router.push({path:'/exam/info'});
                }
            },
            isPassive() {
                var supportsPassiveOption = false;
                try {
                    addEventListener("test", null, Object.defineProperty({}, 'passive', {
                        get: function () {
                            supportsPassiveOption = true;
                        }
                    }));
                } catch(e) {}
                return supportsPassiveOption;
            },
            async GetList(type){
                let info = {
                    is_exam:this.active,
                    keywords:this.keywords,
                    start_time:this.startTime,
                    end_time:this.endTime,
                    page:this.pageNumber,
                    is_end:'',
                }
                if(this.active == 1){
                    info.is_end = 1;
                }
                let data = await this.Ajax.ExamineList(info);
                let now = new Date();
                now = now.getTime();
                let list = data.data.data.map(el=>{
                    el.startTime = window.GetTimeType(parseInt(el.start_time*1000),'yyyy.MM.dd hh:mm:ss');
                    el.endTime = window.GetTimeType(parseInt(el.end_time*1000),'yyyy.MM.dd hh:mm:ss');
                    if(now < el.start_time*1000){ // 未开始 
                        el.status_ = 1;
                    }else if(now > el.end_time*1000){// 已结束
                        el.status_ = 2;
                    }else{
                        el.status_ = 3;
                    }
                    if(el.examInfo.is_join == '1' ){
                        let l = [4,5,6];
                        el.status_ = l[el.examInfo.status];
                    }
                    return el;
                });
                this.total = parseInt(data.data.total);
                if(type == 'Re'){
                    this.dataList = list;
                }else{
                    this.dataList = this.dataList.concat(list);
                }
                this.$nextTick(()=>{
                    this.canGet = true;
                })
            },
            Scroll(event){
                let direction = '';
                if(this.oldScrollTop > event.target.scrollTop){
                    direction = 'up';
                }else{
                    direction = 'down';
                }
                this.oldScrollTop = event.target.scrollTop;
                let scrollBottom = event.target.scrollHeight - event.target.scrollTop - event.target.clientHeight;
                if(direction === 'down' && scrollBottom < 100 && this.canGet && this.total > (this.pageNumber*this.pageSize)){
                    this.canGet = false;
                    this.pageNumber++;
                    this.GetList();
                }
            },
        },
        mounted() {
            this.GetList();
        },
    }
</script>

