import axios from 'axios'
import Qs from 'qs'
import AllUrl from './ipConfig'
import router from '../router'
import { Message } from 'element-ui'

const Axios = axios.create({
    // 总
    baseURL: AllUrl.baseIp,
    timeout: 15000,
    headers: {
        "Content-Type": "application/json",
    },
})
//POST传参序列化(添加请求拦截器)
Axios.interceptors.request.use(config =>{
    console.log(config);
    config.headers.Authorization = sessionStorage.getItem('token') || '';
    return config
},error=>{
    Message.error('请求失败!');
    return Promise.reject(error)
})

//返回状态判断(添加响应拦截器)
Axios.interceptors.response.use(function(response){
    const res = response.data;
    return res;
},function(error){
    return Promise.reject(error.response.data)
})

const Ajax = async ({url,type,data,msg='获取信息失败!'})=>{
    // url = '/api' + url;
    let c = null;
    if(type==='post'){
        // data = Qs.stringify(data);
        try {
            c = await Axios.post(url,data);
        } catch (error) {
            // message.error(msg);
            c = false;
            console.log(error);
        }
    }
    if(type==='get'){
        if(!!data){
            let str = '';
            for (const i in data) {
                str += '&' + i + '=' + data[i];
            }
            str = str.replace('&','?');
            url += str;
        }
        try {
            c = await Axios.get(url);
        } catch (error) {
            // message.error(msg);
            c = false;
            console.log(error);
        }
    }

    // if(c.code == '402'){
    //     Message.error(c.message);
    //     return false;
    // }
    // let type_ = sessionStorage.getItem('YType');
    // if(c.code == '403' && type_){
    //     let id_ = sessionStorage.getItem('YId');
    //     let re = AllUrl.loginUrl;
    //     if(!!type_){
    //         re  += `?type=${type_}&id=${id_}`;
    //     }
    //     Message.warning(c.message);
    //     window.location.href = re;
    //     return false;
    // }

    return c;
}

export default Ajax;