<template>
    <div class="Personne_box">
        <div class="B_item" v-for="(el,index) in personneList" :key="index">
            <div :class="['B_info',el.show?'active':'']" @click="el.show=!el.show">
                <img src="@/assets/img_31.png" alt="">
                <span>
                    {{el.department_name}}
                </span>
            </div>
            <div class="R_item" :style="`height:${el.show?(el.member.length+'rem'):'0'}`">
                <div class="R_info" v-for="(ele,index_) in el.member" :key="index_">
                    <img :src="ele.avatar" alt="">
                    <span>{{ele.username}}</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                personneList:[],
            }
        },
        computed: {
            currentInfo:function(){
                return this.$store.state.Info.activityInfo;
            },
        },
        created() {
            this.getInfo();
        },
        methods: {
            async getInfo(){
                let e = await this.Ajax.ActivityInvite({activity_id:this.currentInfo.id});
                this.personneList = e.data.map(el=>{
                    el.show = false;
                    el.member = el.member.map(ele=>{
                        ele.avatar = !!ele.avatar?ele.avatar:require('@/assets/img_55.png');
                        return ele;
                    })
                    return el;
                })
            }
        },
    }
</script>

