<template>
    <div class="Content_box" style="padding-bottom:1.1rem;" @scroll="Scroll">
        <div class='Home_head'>
            <div class="Home_head_one">
                <img src="@/assets/img_1.png" class="one" alt="" @click="Go(0)">
                <img src="@/assets/img_2.png" alt="" class="two" @click="Go(1)">
                <img src="@/assets/img_3.png" alt="" class="three" @click="Search">
            </div>
            <div class="tabs_">
                <span v-for='(el,index) in tab_list' :key="index" :class="index===active?'active':''" @click="tabChange(index)">
                    {{el}}
                </span>
            </div>
        </div>
        <component ref="Target" :is="tab_list_[active]" :canGet.sync='canGet' />
        <div class="Footer_">
            展示最近一周的活动
        </div>
    </div>
</template>

<script>
    import Examination from './components/examination'
    import Vote from './components/vote'
    import School from './components/School'
    import Message from './components/message'
    import Activity from './components/activity.vue'
    export default {
        components:{Examination,Vote,School,Message,Activity},
        data(){
            return{
                tab_list:['考试','投票','学堂','酷讯','报名'],
                tab_list_:['Examination','Vote','School','Message','Activity'],
                oldScrollTop:0,
                canGet:true,
            }
        },
        created() {
            
        },
        computed: {
            active:function(){
                return this.$store.state.Info.HomeActive;
            },
        },
        methods: {
            Go(type){
                if(type === 0){
                    this.$router.push({path:'/home'});
                }else{
                    this.$router.push({path:'/mine'});
                }
            },
            tabChange(index){
                this.$store.commit('setValue',{key:'HomeActive',value:index});
            },
            Search(){
                this.$router.push({path:'/search'});
            },
            Scroll(event){
                let direction = '';
                if(this.oldScrollTop > event.target.scrollTop){
                    direction = 'up';
                }else{
                    direction = 'down';
                }
                this.oldScrollTop = event.target.scrollTop;
                let scrollBottom = event.target.scrollHeight - event.target.scrollTop - event.target.clientHeight;
                if(direction === 'down' && scrollBottom < 100 && this.canGet){
                    this.canGet = false;
                    this.$refs['Target'].ScrollGet();
                }
            },
        },
    }
</script>

