<template>
    <div class="Enroll_box">

        <div class="Q_item" v-for="(el,index) in questionList" :key="index">
            <h1>
                {{index+1}}.{{el.item_title}}
            </h1>
            <div class="option_list" v-if="el.item_type == '1'">
                <div class="option_item" v-for="(o,index_) in el.item_options" :key="index_" @click="el.value = index_;">
                    <img src="@/assets/img_44.png" v-if="el.value===index_" alt="">
                    <img src="@/assets/img_43.png" v-else alt="">
                    <p>
                        {{o}}
                    </p>
                </div>
            </div>
            <div class="option_list" v-if="el.item_type == '2'">
                <div class="option_item" v-for="(ele,index_) in el.item_options" :key="index_" @click="ele.check=!ele.check">
                    <img src="@/assets/img_44.png" v-if="ele.check" alt="">
                    <img src="@/assets/img_43.png" v-else alt="">
                    <p>
                        {{ele.title}}
                    </p>
                </div>
            </div>  
            <div v-if="el.item_type == '3'">
                <textarea v-model='el.value' placeholder="请输入内容？" id="" cols="30" rows="10"></textarea>
            </div>
        </div>

        <button :class="['Start_btn','active']" @click="Sign">
            提交
        </button>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                questionList:[],
                clickControl:true,
            }
        },
        created() {
            this.getInfo();
        },
        computed: {
            currentInfo:function(){
                return this.$store.state.Info.activityInfo;
            },
        },
        methods: {
            async getInfo(){
                let l = await this.Ajax.ActivityItem({activity_id:this.currentInfo.id});
                this.questionList = l.data.map(el=>{
                    el.value = '';
                    if(el.item_type == 2){
                        el.item_options = el.item_options.map(ele=>{
                            let c = {
                                title:ele,
                                check:false,
                            }
                            return c;
                        })
                    }
                    return el;
                })
                console.log(this.questionList);
                
            },
            async Sign(){
                if(!this.clickControl){
                    return false;
                }
                this.clickControl = false;
                let pan = true;
                let info = {
                    "activity_id":this.currentInfo.id,
                    options:[],
                };
                this.questionList.forEach(el=>{
                    let c = {
                        'item_title':el.item_title,
                        'item_options':'',
                    }
                    if(el.item_type == 1 ){
                        if(el.value!==''){
                            c['item_options'] = el.item_options[el.value];
                        }
                    }
                    if(el.item_type == 2 ){
                        c['item_options'] = [];
                        el.item_options.forEach(ele=>{
                            if(ele.check){
                                c['item_options'].push(ele.title);
                            }
                        });
                    }
                    if(el.item_type == 3){
                        c['item_options'] = el.value;
                    }
                    if(el.is_must == 1){
                        console.log(c.item_options);
                        if(c.item_options.length<=0){
                            pan = false;
                        }
                    } 
                    info.options.push(c);
                })
                if(!pan){
                    this.$message.warning('请完善报名信息！');
                    this.clickControl = true;
                    return false;
                }
                let apply = await this.Ajax.ActivityApply(info);
                if(apply.code == 0){
                    this.$message.success('报名成功！');
                    this.$router.go(-1);
                }else{
                    this.$message.warning(apply.message);
                }
            },
        },
    }
</script>

