<template>
    <div class="School_detail" style="padding-bottom:1rem;">
        <div class="V_item">
            <div class="I_box">
                <img :src="detailInfo.image" class="m_img" alt="">
                <span :class="class_[detailInfo.status]">
                    {{statusList[detailInfo.status]}}
                </span>
            </div>
            <h1 class="title">
                {{detailInfo.title}}
            </h1>
            <h2>
                主题：{{detailInfo.theme}}
            </h2>
            <h2>
                活动开始时间：{{detailInfo.startTime}}
            </h2>
            <h2>
                活动结束时间：{{detailInfo.endTime}}
            </h2>
            <h2>
                发起人：{{detailInfo.author_name}}
            </h2>
            <h2>
                活动地点：{{detailInfo.address}}
            </h2>
            <h2>
                邀请人员： <label v-for="(el,index) in inviteBuAll" :key="index">{{el.department_name}} </label>
                <label class="more" @click="SeePersonne">查看全部</label>
            </h2>
        </div>
        <div class="Countdown" v-if="detailInfo.status=='10'&&this.endTime>0">
            <span>报名倒计时</span>
            <div class="time_">
                <label>
                    {{Countdown.d}}
                </label>
                <em>
                    天:
                </em>
                <label>
                    {{Countdown.h}}
                </label>
                <em>
                    时:
                </em>
                <label>
                    {{Countdown.m}}
                </label>
                <em>
                    分:
                </em>
                <label>
                    {{Countdown.s}}
                </label>
                <em>
                    秒
                </em>
            </div>
        </div>
        <div class="line" v-else></div>
        <div class="detail_info">
            <div class="content" v-html="detailInfo.content">
            </div>
            <div class="info">
                <div class="one">
                    <img src="@/assets/img_39.png" alt="">
                    <span>
                        {{detailInfo.comment_num}}
                    </span>
                </div>
                <div class="one"  @click="ChangeLike()">
                    <img src="@/assets/img_37.png" v-if="!Fabulous" alt="">
                    <img src="@/assets/img_38.png" v-else alt="">
                    <span>
                        {{detailInfo.like_num}}
                    </span>
                </div>
                <div class="two" @click="RShow(0)">
                    写评论
                </div>
            </div>
        </div>
        <div class="line"></div>
        <div class="Sign_list">
            <div class="info">
                <p>
                    <span>{{detailInfo.apply_num}}人</span>已报名 {{detailInfo.limit_person_num==0?'人数不限':`${detailInfo.limit_person_num}人`}}
                </p>
                <div @click="SeeSign()">
                    <span>查看报名单</span>
                    <img src="@/assets/img_4.png" alt="">
                </div>
            </div>
            <div class="img_list">
                <img v-for="(el,index) in signList" :src="el.avatar"  :key="index" alt="">
            </div>
        </div>
        <div class="line"></div>
        <div class="comment" v-if='!!commentList&&commentList.length>0'>
            <div class="comment_item"  v-for="(el,index) in commentList" :key="index">
                <div class="comment_item_main" @click="RShow(1,el,index)">
                    <div class="I_box">
                        <img :src="!!el.avatar?el.avatar:require('@/assets/img_55.png')" alt="">
                    </div>
                    <div class="comment_item_content">
                        <h1>
                            {{el.username}}
                            <span v-if="el.user_id==detailInfo.author_id">作者</span>
                            <div class="zan" v-if="el.user_id != userInfo.uid">
                                <img src="@/assets/img_38.png" v-if="!!el.like" @click.stop="el.like = false;ActivityCommentCancelLike(el);" alt="">
                                <img src="@/assets/img_37.png" v-else alt="" @click.stop="el.like = true;ActivityCommentLike(el)" >
                            </div>
                            <div v-if='el.user_id == userInfo.uid && el.del == 1' class="del" @click.stop="DelComment(el)">
                                删除
                            </div>
                        </h1>
                        <h2>
                            {{GetTimeOne(el.created_at)}}
                        </h2>
                        <p v-if="el.del == 1">
                            {{el.content}}
                        </p>
                        <p v-else>
                            该评论已删除
                        </p>
                    </div>
                </div>
                <div class="comment_item_reply" v-if="!!el.children&&el.children.length>0">
                    <div v-if="!el.moreShow">
                        <div class="comment_item_reply_item" v-for="(ele,ind) in el.children.slice(0,3)" :key="ind" @click="RShow(2,el,ele)">
                            <div class="comment_item_main">
                                <div class="I_box">
                                    <img :src="!!ele.avatar?ele.avatar:require('@/assets/img_55.png')" alt="">
                                </div>
                                <div class="comment_item_content">
                                    <h1>
                                        {{ele.username}}
                                        <span v-if="ele.user_id==detailInfo.author_id">作者</span>
                                        <!-- <div class="zan" v-if="ele.user_id != userInfo.uid">
                                            <img src="@/assets/img_38.png" v-if="!!ele.like" @click.stop="ele.like = false;ActivityCommentCancelLike(ele);" alt="">
                                            <img src="@/assets/img_37.png" v-else alt="" @click.stop="ele.like = true;ActivityCommentLike(ele)" >
                                        </div> -->
                                        <div v-if="ele.user_id == userInfo.uid && ele.del == 1" class="del"  @click.stop="DelComment(ele)">
                                            删除
                                        </div>
                                    </h1>
                                    <h2>
                                        {{GetTimeOne(ele.created_at)}}
                                    </h2>
                                    <p v-if="ele.del == 1">
                                        <span v-if="!!ele.quest&&!!ele.quest.username">
                                            回复 <em>{{ele.quest.username}}</em>:
                                        </span>
                                        {{ele.content}}
                                    </p>
                                    <p v-else>
                                        该评论已删除
                                    </p>
                                </div>
                            </div>
                        </div>
                        <div class="more" @click="el.moreShow = true;">
                            展开 {{el.children.length-3}} 条回复
                        </div>
                    </div>
                    <div v-else>
                        <div class="comment_item_reply_item" v-for="(ele,ind) in el.children" :key="ind" @click="RShow(2,el,ele)">
                            <div class="comment_item_main">
                                <div class="I_box">
                                    <img :src="!!ele.avatar?ele.avatar:require('@/assets/img_55.png')" alt="">
                                </div>
                                <div class="comment_item_content">
                                    <h1>
                                        {{ele.username}}
                                        <span v-if="ele.user_id==detailInfo.author_id">作者</span>
                                        <!-- <div class="zan" v-if="ele.user_id != userInfo.uid">
                                            <img src="@/assets/img_38.png" v-if="!!ele.like" @click.stop="ele.like = false;ActivityCommentCancelLike(ele);" alt="">
                                            <img src="@/assets/img_37.png" v-else alt="" @click.stop="ele.like = true;ActivityCommentLike(ele)" >
                                        </div> -->
                                        <div v-if="ele.user_id == userInfo.uid && ele.del == 1" class="del"  @click.stop="DelComment(ele)">
                                            删除
                                        </div>
                                    </h1>
                                    <h2>
                                        {{GetTimeOne(ele.created_at)}}
                                    </h2>
                                    <p v-if="ele.del == 1">
                                        <span v-if="!!ele.quest&&!!ele.quest.username">
                                            回复 <em>{{ele.quest.username}}</em>:
                                        </span>
                                        {{ele.content}}
                                    </p>
                                    <p v-else>
                                        该评论已删除
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="No_data" v-else>
            <img src="@/assets/img_16.png" alt="">
            <span>
                暂无数据
            </span>
        </div>
        <div class="R_cover" v-if="replyShow" @click="replyShow=false"></div>
        <div :class="['Reply_box',replyShow?'active':'']">
            <div class="Reply_box_r" v-if="replayValue===''&&replayName!=''">回复 <span>{{replayName}}</span></div>
            <input type="text" v-model="replayValue" :placeholder="placeStr" maxlength="100">
            <button @click="ReplayInfo">发布</button>
        </div>
        <div class="C_cover" v-show="CancelShow"></div>
        <div class="Cancel_box" v-if="CancelShow">
            <h1>
                提示
            </h1>
            <h2>
                确认取消报名吗？
            </h2>
            <div>
                <button class="btn_cancel" style="margin-right:.55rem;" @click="CancelShow=false;">取消</button>
                <button class="btn_confirm" @click="CancelConfrim()">确定</button>
            </div>
        </div>

        <button v-if="detailInfo.status=='10'&& !replyShow" :class="['Start_btn',detailInfo.apply===null?'active':'']" @click="Sign">
            {{detailInfo.apply===null?'报名':'取消报名'}}
        </button>
    </div>
</template>

<script>
    import IpConfig from '../../utils/ipConfig'
    export default {
        data() {
            return {
                Fabulous:false,
                statusList:{
                    '10':'未开始',
                    '20':'进行中',
                    '30':'已结束',
                },
                class_:{
                    '10':'S_1',
                    '20':'S_2',
                    '30':'S_3',
                },
                commentList:[],
                placeStr:'说点什么吧~',
                replyShow:false,
                signList:[1,2,3,4,5,6],
                CancelShow:false,
                weekday:["星期日","星期一","星期二","星期三","星期四","星期五","星期六"],
                replayName:'',
                replayValue:'',
                detailInfo:{},
                r_id:'',

                endTime:0,
                endCount:null,
                replyType:0,
                replyInfo:{},
                replayControl:true,
                inviteBuAll:[],

                zanControl:true,
                signControl:true,
            }
        },
        created() {
            this.GetDetail();
        },
        mounted() {
            
        },
        computed: {
            currentInfo:function(){
                return this.$store.state.Info.activityInfo;
            },
            Countdown:function(){
                let time = this.GetTime(this.endTime);
                return time;
            },
            userInfo:function(){
                return this.$store.state.Info.userInfo;
            },
        },
        methods: {
            GetTime(time){
                let d = parseInt(time/(60*60*24));
                let h = parseInt((time%(60*60*24))/(60*60))>=10?parseInt((time%(60*60*24))/(60*60)):('0'+parseInt((time%(60*60*24))/(60*60)));
                let m = parseInt((time%(60*60*24))%(60*60)/60)>=10?parseInt((time%(60*60*24))%(60*60)/60):('0'+parseInt((time%(60*60*24))%(60*60)/60));
                let s = parseInt(time%60)>=10?parseInt(time%60):('0'+parseInt(time%60));
                return {d,h,m,s}
            },
            GetTimeOne(time){
                return window.GetTimeType(parseInt(time*1000),'yyyy.MM.dd hh:mm:ss')
            },
            Replay(el,index){

            },
            RShow(type,el,ele){
                this.replyType = type;
                if(type === 0){
                    this.placeStr = '说点什么吧~';
                    this.replayName = '';
                    this.replayValue = '';
                    this.replyShow = true;
                }
                if(type === 1 || type === 2){
                    this.replyInfo = el;
                    if(el.del != 1){
                        this.$message.warning('该评论已删除！');
                        return false;
                    }
                    if(type == 2 && !!ele && ele.del != 1){
                        this.$message.warning('该评论已删除！');
                        return false;
                    }
                    if(type === 1){
                        if(el.user_id == this.userInfo.uid){
                            this.$message.warning('不能回复自己!');
                            console.log('自己不能回复自己');
                            return false;
                        }
                        this.replayName = el.username;
                    }
                    if(type === 2){
                        if(ele.user_id == this.userInfo.uid){
                            this.$message.warning('不能回复自己!');
                            console.log('自己不能回复自己');
                            return false;
                        }
                        this.replayName = ele.username;
                    }
                    this.placeStr = '';
                    this.replayValue = '';
                    this.replyShow = true;
                    if(type === 2){
                        this.r_id = ele.id;
                    }else{
                        this.r_id = 0;
                    }
                }
            },
            async ReplayInfo(){
                if(!this.replayControl){
                    return false;
                }
                this.replayControl = false;
                let info = {
                    "activity_id":this.detailInfo.id,
                    "content":this.replayValue,
                    "pid":0,
                    rid:0,
                }
                if(this.replyType === 0){
                    info.pid = 0;
                    info.rid = 0;
                }else{
                    info.pid = this.replyInfo.id;
                    info.rid = this.r_id;
                }
                info.content = info.content.trim();
                if(info.content==''){
                    this.$message.warning('请输入内容！');
                    return false;
                }
                let re = await this.Ajax.ActivityComment(info);
                if(re.code === 0){
                    this.$message.warning('添加评论成功！');
                    this.replayValue = '';
                    this.replyShow = false;
                }else{
                    this.$message.error('添加评论失败！');
                }
                this.replayControl = true;
                this.GetDetail();
            },
            SeePersonne(){
                this.$router.push('/activity/personne');
            },
            async Sign(){
                if(!this.signControl){
                    return false;
                }
                this.signControl = false;
                let now = new Date();
                let endTime = this.detailInfo.close_date*1000 - now.getTime();
                if(endTime<=0){
                    this.$message.warning('已超过报名时间!');
                    return false;
                }

                if(this.detailInfo.apply!==null){
                    this.CancelShow = true;
                    this.signControl = true;
                }else{
                    if(this.detailInfo.apply_num == this.detailInfo.limit_person_num && this.detailInfo.apply_num != 0){
                        this.$message.warning('报名已达到上限!');
                        this.signControl = true;
                    }else{
                        let l = await this.Ajax.ActivityItem({activity_id:this.detailInfo.id});
                        if(!!l.data && l.data.length>0 && !!l.data[0].id){
                            this.signControl = true;
                            this.$router.push('/activity/enroll');
                        }else{
                            this.SignNo();
                        }
                    }
                }
            },
            async SignNo(){
                let info = {
                    "activity_id":this.detailInfo.id,
                    option:[],
                };
                let apply = await this.Ajax.ActivityApply(info);
                if(apply.code == 0){
                    this.$message.success('报名成功！');
                    this.GetDetail();
                }else{
                    this.$message.warning(apply.message);
                }
                this.signControl = true;
            },
            SeeSign(){
                this.$router.push('/activity/registration');
            },
            async CancelConfrim(){
                this.CancelShow = false;
                let cancel = await this.Ajax.ActivityRevoke({activity_id:this.detailInfo.id});
                this.GetDetail();
            },
            async ActivityCommentLike(el){
                let like = await this.Ajax.ActivityCommentLike({comment_id:el.id});
            },
            async ActivityCommentCancelLike(el){
                let unlike = await this.Ajax.ActivityCancelCommentLike({'comment_id':el.id});
            },
            async ChangeLike(){
                if(!this.zanControl){
                    return false;
                }
                this.zanControl = false;
                if(this.Fabulous){
                    let CancelLike = await this.Ajax.ActivityCancelLike({activity_id:this.detailInfo.id});
                }else{
                    let like = await this.Ajax.ActivityLike({id:this.detailInfo.id});
                    console.log(like);
                }
                this.GetDetail();
            },
            GetQueryString (name) {
                var url = window.location.href.split('?')[1];
                let localType = new URLSearchParams(url).get(name);
                return localType;
            },
            async DelComment(el){
                let del = await this.Ajax.ActivityDelComment({id:el.id});
                this.GetDetail();
            },
            async GetDetail(){
                let id = this.currentInfo.id;
                if(!!!id){
                    id = this.GetQueryString('id');
                }
                let [detail,invite,entry] = await Promise.all([
                    this.Ajax.ActivityDetail({id}),
                    this.Ajax.ActivityInvite({activity_id:id}),
                    this.Ajax.ActivityEntry({activity_id:id}),
                ]);
                
                let info = detail.data;
                
                if(detail.code == 402){
                    this.$message.warning('无权限查看！');
                    return false;
                }
                if(detail.code == 403){
                    this.$message.warning('尚未登录，将跳转登录请登录后查看！');
                    window.location.href = this.AllUrl.loginUrl + `?type=${this.$route.meta.type}&id=${id}`;
                    return false;
                }
                if(detail.code != 0){
                    this.$message.warning(detail.message);
                    return false;
                }
                info.startTime = window.GetTimeType(parseInt(info.start_time*1000),'yyyy.MM.dd hh:mm:ss');
                info.endTime = window.GetTimeType(parseInt(info.end_time*1000),'yyyy.MM.dd hh:mm:ss');
                info.image = JSON.parse(info.image);
                if(info.like === null){
                    this.Fabulous = false;
                }else{
                    this.Fabulous = true;
                }
                if(!!info.image&&info.image.length>0){
                    info.image = info.image[0];
                }else{
                    info.image = require('@/assets/img_14.png');
                }
                this.commentList = info.comment.map(el=>{
                    if(!!el.children && el.children.length>3){
                        el.moreShow = false;
                    }else{
                        el.moreShow = true;
                    }
                    return el;
                });

                let signList = entry.data.map(ele=>{
                    ele.avatar = !!ele.avatar?ele.avatar:require('@/assets/img_55.png');
                    return ele;
                });
                if(signList.length>8){
                    signList = signList.slice(0,8);
                }
                this.signList = signList;
                this.inviteBuAll = invite.data;
                if(info.status == '10'){ // 未开始
                    let now = new Date();
                    let endTime = info.close_date*1000 - now.getTime();
                    if(endTime<0){
                        endTime = 0;
                    }
                    this.endTime = endTime/1000;
                    this.endCount = setInterval(()=>{
                        if(this.endTime>0){
                            this.endTime--;
                        }else{
                            // this.detailInfo.status = '20';
                            clearInterval(this.endCount);
                            this.endCount = null;
                        }
                    },1000)
                }
                this.detailInfo = info;
                this.zanControl = true;
                this.$nextTick(()=>{
                    this.WixinSet();
                })
            },
            async WixinSet(){
                let url = window.location.href;
                url = encodeURIComponent(url);
                let config = await this.Ajax.WxConfig({url});
                console.log(config);
                window.wx.config({
                    beta: true,
                    debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
                    ...config.data,
                    jsApiList: ['onMenuShareAppMessage','onMenuShareTimeline','showOptionMenu'] // 必填，需要使用的JS接口列表
                });
                window.wx.ready(()=>{
                    window.wx.showOptionMenu();

                    let img = !!this.detailInfo.Image&&this.detailInfo.Image.length>0?this.detailInfo.Image[0]:'';
                    let link = `${IpConfig.baseIp}/html/index.html#/activity/detail?type=activity&id=${this.detailInfo.id}`;
                    window.wx.onMenuShareAppMessage({
                        title: this.detailInfo.title, // 分享标题
                        desc: this.detailInfo.title, // 分享描述
                        link: link, // 分享链接
                        imgUrl: img, // 分享图标
                        success: function () {
                            // 用户确认分享后执行的回调函数
                            console.log('success');
                        },
                        cancel: function () {
                            // 用户取消分享后执行的回调函数
                            console.log('cancel');
                        }
                    })
                    window.wx.onMenuShareTimeline({
                        title: this.detailInfo.title, // 分享标题
                        link: link, // 分享链接
                        imgUrl: img, // 分享图标
                        success: function () {
                            // 用户确认分享后执行的回调函数
                            console.log('success');
                        },
                        cancel: function () {
                            // 用户取消分享后执行的回调函数
                            console.log('cancel');
                        }
                    })
                })  
            },
        },
    }
</script>

<style lang="scss" scoped>

</style>