<template>
    <div :style="`padding-left:${(level===0?0:1)*0.3}rem;background:#fff;`">
        <div :class="['Menu_item',active==menuInfo.id?'active':'']" style="padding:0 .3rem;" @click="ChangeActive(menuInfo.id,level)">
            <div :style="`height:10px;padding-left:${level*0.3}rem;`"></div>
            <div :class="['i_box',show?'active':'']" v-if="!!menuInfo.articleCategories&&menuInfo.articleCategories.length>0" @click.stop="toggleChange">
                <img  src="@/assets/img_31.png" alt="" >
            </div>
            <p>
                {{menuInfo.name}}
            </p>
            <span v-if="!!menuInfo.firstCount">
                {{menuInfo.firstCount.length}}
            </span>
            <span v-if="!!menuInfo.secondCount">
                {{menuInfo.secondCount.length}}
            </span>
            <span v-if="!!menuInfo.thirdCount">
                {{menuInfo.thirdCount.length}}
            </span>
        </div>
        <div class="Menu_item_list" :style="`height:${height}`" v-if="!!menuInfo.articleCategories&&menuInfo.articleCategories.length>0">
            <div ref="articleCategories">
                <Menu v-for="(el,index) in menuInfo.articleCategories" :key="index" :menuInfo='el' :level='level+1' :active.sync='active' @ChangeActive='ChangeActive' />
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name:'Menu',
        props:['menuInfo','level','active'],
        data(){
            return{
                show:false,
                height:0,
                heightCount:null,
            }
        },
        methods: {
            toggleChange(){
                let tar = this.$refs['articleCategories'];
                if(!!this.heightCount){
                    clearTimeout(this.heightCount);
                }
                if(this.show){
                    this.height = tar.clientHeight + 'px';
                    setTimeout(()=>{
                        this.height = 0;

                    },50)
                }else{
                    this.height = tar.clientHeight + 'px';
                    this.heightCount = setTimeout(()=>{
                        this.height = 'initial';
                        this.heightCount = null;
                    },500)
                }
                this.show = !this.show;
            },
            ChangeActive(id,level){
                this.$emit('ChangeActive',id,level);
            },
        },
    }
</script>

