<template>
    <div class="School_detail">
        <div class="V_item">
            <div class="I_box">
                <img :src="detailInfo.image" class="m_img" alt="">
            </div>
            <h1 class="title">
                {{detailInfo.title}}
            </h1>
            <h2>
                分类：
                <label v-if="!!detailInfo.first&&!!detailInfo.first.name">{{detailInfo.first.name}}</label>
                <label v-if="!!detailInfo.second&&!!detailInfo.second.name">>{{detailInfo.second.name}}</label>
                <label v-if="!!detailInfo.third&&!!detailInfo.third.name">>{{detailInfo.third.name}}</label>
            </h2>
            <h2>
                发起人：{{detailInfo.author_name}}
            </h2>
            <h2>
                发布时间：{{detailInfo.created_at}}
            </h2>
            <h2 v-if='!!detailInfo.abstract'>
                摘要：{{detailInfo.abstract}}
            </h2>
        </div>
        <div class="line"></div>
        <div class="detail_info">
            <div class="content" v-html="detailInfo.content">
            </div>
            <div class="enclosure" v-for="(el,index) in enclosureList" :key="index" @click="Down(el)">
                <img src="@/assets/img_41.png" alt="">
                <span>
                    {{el.name}}
                </span>
                <img src="@/assets/img_42.png" alt="">
            </div>
            <div class="info">
                <div class="one">
                    <img src="@/assets/img_39.png" alt="">
                    <span>
                        {{detailInfo.comment_number}}
                    </span>
                </div>
                <div class="one" @click="ChangeLike()">
                    <img src="@/assets/img_37.png" v-if="!Fabulous" alt="">
                    <img src="@/assets/img_38.png" v-else alt="">
                    <span>
                        {{detailInfo.like_number}}
                    </span>
                </div>
                <div class="two" @click="RShow(0)">
                    写评论
                </div>
            </div>
        </div>
        <div class="line"></div>
        <div class="comment" v-if='!!commentList&&commentList.length>0'>
            <div class="comment_item"  v-for="(el,index) in commentList" :key="index">
                <div class="comment_item_main" @click="RShow(1,el,index)">
                    <div class="I_box">
                        <img src="@/assets/img_55.png" alt="">
                    </div>
                    <div class="comment_item_content">
                        <h1>
                            {{el.username}}
                            <span v-if="el.user_id==detailInfo.author_id">作者</span>
                            <div class="zan" v-if="el.user_id != userInfo.uid">
                                <img src="@/assets/img_38.png" v-if="!!el.like" @click.stop="el.like = false;MessageCommentCancelLike(el);" alt="">
                                <img src="@/assets/img_37.png" v-else alt="" @click.stop="el.like = true;MessageCommentLike(el)" >
                            </div>
                            <div v-if='el.user_id == userInfo.uid && el.del == 1' class="del" @click="DelComment(el)">
                                删除
                            </div>
                        </h1>
                        <h2>
                            {{GetTime(el.created_at)}}
                        </h2>
                        <p v-if="el.del == 1">
                            {{el.content}}
                        </p>
                        <p v-else>
                            该评论已删除
                        </p>
                    </div>
                </div>
                <div class="comment_item_reply" v-if="!!el.children&&el.children.length>0">
                    <div v-if="!el.moreShow">
                        <div class="comment_item_reply_item" v-for="(ele,ind) in el.children.slice(0,3)" :key="ind" @click="RShow(2,el,ele)">
                            <div class="comment_item_main">
                                <div class="I_box">
                                    <img src="@/assets/img_55.png" alt="">
                                </div>
                                <div class="comment_item_content">
                                    <h1>
                                        {{ele.username}}
                                        <span v-if="ele.user_id==detailInfo.author_id">作者</span>
                                        <!-- <div class="zan" v-if="ele.user_id != userInfo.uid">
                                            <img src="@/assets/img_38.png" v-if="!!ele.like" @click.stop="ele.like = false;MessageCommentCancelLike(ele);" alt="">
                                            <img src="@/assets/img_37.png" v-else alt="" @click.stop="ele.like = true;MessageCommentLike(ele)" >
                                        </div> -->
                                        <div v-if="ele.user_id == userInfo.uid && ele.del == 1" class="del"  @click="DelComment(ele)">
                                            删除
                                        </div>
                                    </h1>
                                    <h2>
                                        {{GetTime(ele.created_at)}}
                                    </h2>
                                    <p v-if="ele.del == 1">
                                        <span v-if="!!ele.quest&&!!ele.quest.username">
                                            回复 <em>{{ele.quest.username}}</em>:
                                        </span>
                                        {{ele.content}}
                                    </p>
                                    <p v-else>
                                        该评论已删除
                                    </p>
                                </div>
                            </div>
                        </div>
                        <div class="more" @click="el.moreShow = true;">
                            展开 {{el.children.length-3}} 条回复
                        </div>
                    </div>
                    <div v-else>
                        <div class="comment_item_reply_item" v-for="(ele,ind) in el.children" :key="ind" @click="RShow(2,el,ele)">
                            <div class="comment_item_main">
                                <div class="I_box">
                                    <img src="@/assets/img_55.png" alt="">
                                </div>
                                <div class="comment_item_content">
                                    <h1>
                                        {{ele.username}}
                                        <span v-if="ele.user_id==detailInfo.author_id">作者</span>
                                        <!-- <div class="zan" v-if="ele.user_id != userInfo.uid">
                                            <img src="@/assets/img_38.png" v-if="!!ele.like" @click.stop="ele.like = false;MessageCommentCancelLike(ele);" alt="">
                                            <img src="@/assets/img_37.png" v-else alt="" @click.stop="ele.like = true;MessageCommentLike(ele)" >
                                        </div> -->
                                        <div v-if="ele.user_id == userInfo.uid && ele.del == 1" class="del"  @click="DelComment(ele)">
                                            删除
                                        </div>
                                    </h1>
                                    <h2>
                                        {{GetTime(ele.created_at)}}
                                    </h2>
                                    <p v-if="ele.del == 1">
                                        <span v-if="!!ele.quest&&!!ele.quest.username">
                                            回复 <em>{{ele.quest.username}}</em>:
                                        </span>
                                        {{ele.content}}
                                    </p>
                                    <p v-else>
                                        该评论已删除
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="No_data" v-else>
            <img src="@/assets/img_16.png" alt="">
            <span>
                暂无数据
            </span>
        </div>
        <div class="R_cover" v-if="replyShow" @click="replyShow=false"></div>
        <div :class="['Reply_box',replyShow?'active':'']">
            <div class="Reply_box_r" v-if="replayValue===''&&replayName!=''&&replyType!=0">回复 <span>{{replayName}}</span></div>
            <input type="text" :placeholder="placeStr" v-model="replayValue" maxlength="100">
            <button @click="ReplayInfo">发布</button>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                enclosureList:[],
                Fabulous:false,
                commentList:[],
                placeStr:'说点什么吧~',
                replyShow:false,
                replayValue:'',
                replayName:'',
                detailInfo:{},
                replyType:'',
                replyInfo:{},
                r_id:'',
            }
        },
        created() {
            this.GetInfo();
        },
        computed: {
            currentInfo:function(){
                return this.$store.state.Info.messageInfo;
            },
            userInfo:function(){
                return this.$store.state.Info.userInfo;
            },
        },
        mounted() {
            this.StopW();
        },
        methods: {
            GetTime(time){
                return window.GetTimeType(parseInt(time*1000),'yyyy.MM.dd hh:mm:ss')
            },
            async GetInfo(){
                let detail = await this.Ajax.MessageDetail({id:this.currentInfo.id});
                let detailInfo = detail.data;
                if(detail.code != 0){
                    this.$message.warning(detail.message);
                    return false;
                }
                if(detailInfo.like === null){
                    this.Fabulous = false;
                }else{
                    this.Fabulous = true;
                }
                detailInfo.image = JSON.parse(detailInfo.image);
                detailInfo.created_at = window.GetTimeType(parseInt(detailInfo.created_at*1000),'yyyy.MM.dd hh:mm:ss');
                let attachment = !!detailInfo.attachment?JSON.parse(detailInfo.attachment):[];
                this.enclosureList = attachment.map(el=>{
                    let c = el.split('/');
                    let item = {
                        name:c[c.length-1],
                        url:el,
                    }
                    return item;
                })
                this.commentList = detailInfo.comments.map(el=>{
                    if(!!el.children && el.children.length>3){
                        el.moreShow = false;
                    }else{
                        el.moreShow = true;
                    }
                    return el;
                });
                if(!!detailInfo.image&&detailInfo.image.length>0){
                    detailInfo.image = detailInfo.image[0];
                }else{
                    detailInfo.image = require('@/assets/img_14.png');
                }
                if(detailInfo.is_secrecy == 1){
                    this.WixinStop();
                }
                this.detailInfo = detailInfo;
            },
            Replay(el,index){

            },
            async ReplayInfo(){
                let info = {
                    "article_id":this.currentInfo.id,
                    "content":this.replayValue,
                    "pid":0,
                    rid:0,
                }
                if(this.replyType === 0){
                    info.pid = 0;
                    info.rid = 0;
                }else{
                    info.pid = this.replyInfo.id;
                    info.rid = this.r_id;
                }
                info.content = info.content.trim();
                if(info.content==''){
                    this.$message.warning('请输入内容！');
                    return false;
                }
                let re = await this.Ajax.CreateComment(info);
                if(re.code === 0){
                    this.$message.warning('添加评论成功！');
                    this.replayValue = '';
                    this.replyShow = false;
                }else{
                    this.$message.error('添加评论失败！');
                }
                this.GetInfo();
            },
            RShow(type,el,ele){
                this.replyType = type;
                if(type === 0){
                    this.placeStr = '说点什么吧~';
                    this.replyShow = true;
                }
                if(type === 1 || type === 2){
                    this.replyInfo = el;
                    if(type === 1){
                        if(el.user_id == this.userInfo.uid){
                            this.$message.warning('不能回复自己!');
                            console.log('自己不能回复自己');
                            return false;
                        }
                        this.replayName = el.username;
                    }
                    if(type === 2){
                        if(ele.user_id == this.userInfo.uid){
                            this.$message.warning('不能回复自己!');
                            console.log('自己不能回复自己');
                            return false;
                        }
                        this.replayName = ele.username;
                    }
                    this.placeStr = '';
                    this.replyShow = true;
                    if(type === 2){
                        this.r_id = ele.id;
                    }else{
                        this.r_id = 0;
                    }
                }
            },
            
            async ChangeLike(){
                if(this.Fabulous){
                    let CancelLike = await this.Ajax.MessageCancelLike({article_id:this.currentInfo.id});
                }else{
                    let like = await this.Ajax.MessageLike({id:this.currentInfo.id});
                    console.log(like);
                }
                this.GetInfo();
            },
            Down(el){
                let eleLink = document.createElement('a');
                eleLink.download = el.name;
                eleLink.style.display = 'none';
                eleLink.href = el.url;
                document.body.appendChild(eleLink);
                eleLink.click();
                document.body.removeChild(eleLink);
            },
            async MessageCommentLike(el){
                let like = await this.Ajax.MessageCommentLike({comment_id:el.id});
            },
            async MessageCommentCancelLike(el){
                let unlike = await this.Ajax.MessageCommentCancelLike({'comment_id':el.id});
            },
            async DelComment(el){
                let del = await this.Ajax.DelComment({id:el.id});
                this.GetInfo();
            },
            async WixinStop(){
                let url = window.location.href.split('#')[0];
                let config = await this.Ajax.WxConfig({url});
                console.log(config);
                window.wx.config({
                    beta: true,
                    debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
                    ...config.data,
                    jsApiList: ['hideOptionMenu'] // 必填，需要使用的JS接口列表
                });
                window.wx.ready(()=>{
                    wx.hideOptionMenu();
                })
            },
            StopW(){
                if(document.addEventListener){
                    document.addEventListener('WeixinJSBridgeReady',HideMenu,false); 
                }else if(document.attachEvent){
                    document.attachEvent('WeixinJSBridgeReady' , HideMenu);
                    document.attachEvent('onWeixinJSBridgeReady' , HideMenu); 
                }
                
            },
            HideMenu(){
                WeixinJSBridge.call('hideOptionMenu');
            },
        },
    }
</script>

