<template>
    <div class="Mine_box">
        <div class="head">
            <img src="@/assets/img_48.png" class="one" alt="" @click="Go('/home')">
            <img src="@/assets/img_50.png" alt="" class="two" @click="Go('/mine')">   
        </div>
        <div class="User_info">
            <img :src="userInfo.avatar" v-if="!error&&!!userInfo.avatar" @error="error=true;" alt="">
            <img src="@/assets/img_55.png" v-else alt="">
            <span>{{userInfo.username}}</span>
        </div>
        <div class="T_item" v-for="(el,index) in typeList" :key="index" @click="Go(el.path)">
            <img :src="el.img" alt="">
            <div class="info">
                <span>
                    {{el.title}}
                </span>
                <img src="@/assets/img_4.png" alt="">
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                typeList:[
                    {
                        img:require('@/assets/img_47.png'),
                        title:'考试',
                        path:'/mine/exam',
                    },
                    {
                        img:require('@/assets/img_49.png'),
                        title:'投票',
                        path:'/mine/vote',
                    },
                    {
                        img:require('@/assets/img_46.png'),
                        title:'报名',
                        path:'/mine/activity',
                    },
                ],
                error:false,
            }
        },
        computed: {
            userInfo:function(){
                return this.$store.state.Info.userInfo;
            },
        },
        mounted() {
            // this.WixinStop();
        },
        methods: {
            Go(path){
                this.$router.push(path);
            },
            async WixinStop(){
                let url = window.location.href;
                url = encodeURIComponent(url);
                let config = await this.Ajax.WxConfig({url});
                console.log(config);
                window.wx.config({
                    beta: true,
                    debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
                    ...config.data,
                    jsApiList: ['hideMenuItems'] // 必填，需要使用的JS接口列表
                });
                window.wx.ready(()=>{
                    window.wx.hideMenuItems({
                        menuList:['menuItem:share:appMessage','menuItem:share:wechat','menuItem:favorite'],
                    });
                })  
            },
        },
    }
</script>

