<template>
    <div class="List_box">
        <SchoolItem  v-for="(el,index) in dataList" :Item='el' :key="index" @Detail="Detail" />
    </div>
</template>

<script>
    export default {
        props:['keywords'],
        data() {
            return {
                dataList:[],
                pageSize:10,
                pageNumber:1,
                total:0,
            }
        },
        created() {
            this.GetList();
        },
        watch: {
            keywords:function(){
                this.ReGet();
            }
        },
        methods: {
            More(){
                this.$router.push({path:'/school/home'});
            },
            Detail(item){
                this.$store.commit('setValue',{key:'schoolInfo',value:item});
                this.$router.push({path:'/school/detail'});
            },
            ReGet(){
                this.total = 0;
                this.pageNumber = 10;
                this.dataList = [];
                this.GetList();
            },
            async GetList(){
                let info = {
                    page:this.pageNumber,
                    type:2,
                    keywords:this.keywords,
                    is_index:'',
                    is_read:'',
                }
                let data = await this.Ajax.MessageList(info);
                let list = data.data.data.map(el=>{
                    el.created_at = window.GetTimeType(parseInt(el.created_at*1000),'yyyy.MM.dd hh:mm:ss');
                    el.image = JSON.parse(el.image);
                    if(!!el.image&&el.image.length>0){
                        el.image = el.image[0];
                    }else{
                        el.image = require('@/assets/img_14.png');
                    }
                    return el;
                })
                this.total = parseInt(data.data.total);
                this.dataList = this.dataList.concat(list);
                this.$nextTick(()=>{
                    this.$emit('ChangeCan',this.dataList.length);
                })
            },
            ScrollGet(){
                if(this.total > (this.pageNumber*this.pageSize)){
                    this.pageNumber++;
                    this.GetList();
                }else{
                    console.log('not more');
                    this.$emit('ChangeCan',this.dataList.length);
                }
            },
        },
    }
</script>

