<template>
    <div class="Registration_box">
        <h1 class="title">
            <span>{{allList.length}}人</span>已报名
        </h1>
        <div class="r_item" v-for="(el,index) in allList" :key="index">
            <img :src="el.avatar" alt="">
            <div class="info">
                <span>{{el.username}}</span>
                <label>{{el.time}}</label>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                allList:[],
            }
        },
        computed: {
            currentInfo:function(){
                return this.$store.state.Info.activityInfo;
            },
        },
        created() {
            this.getInfo();
        },
        methods: {
            async getInfo(){
                let e = await this.Ajax.ActivityEntry({activity_id:this.currentInfo.id});
                this.allList = e.data.map(el=>{
                    el.time = window.GetTimeType(parseInt(el.apply_at*1000),'yyyy.MM.dd hh:mm:ss');
                    el.avatar = !!el.avatar?el.avatar:require('@/assets/img_55.png');
                    return el;
                })
            }
        },
    }
</script>

