<template>
    <div class="Vote_detail" @scroll='Scroll' >
        <div class="V_item" style="padding-bottom:0;margin:0;">
            <div class="I_box">
                <img :src="detailInfo.image" class="m_img" alt="">
                <span :class="class_[detailInfo.status]">
                    {{statusList[detailInfo.status]}}
                </span>
            </div>
            <h1 class="title">
                {{detailInfo.title}}
            </h1>
            <h4>
                ({{detailInfo.option_type=='1'?'单选':'多选'}}) ({{detailInfo.is_repetition=='1'?'可重复投票':'只可投一票'}}) ({{detailInfo.vote_type=='1'?'实名':'匿名'}}投票)
            </h4>
            <h2>
                投票时间：{{detailInfo.start}}-{{detailInfo.end}}
            </h2>
            <h2>
                发起人：{{detailInfo.author_name}}
            </h2>
            <h2>
                参与人数：{{detailInfo.vote_num}}人
            </h2>
            
            <div class="lien"></div>
        </div>
        <div class="Countdown" v-if="detailInfo.status=='10'&&this.endTime>0">
            <span>投票倒计时</span>
            <div class="time_">
                <label>
                    {{Countdown.d}}
                </label>
                <em>
                    天:
                </em>
                <label>
                    {{Countdown.h}}
                </label>
                <em>
                    时:
                </em>
                <label>
                    {{Countdown.m}}
                </label>
                <em>
                    分:
                </em>
                <label>
                    {{Countdown.s}}
                </label>
                <em>
                    秒
                </em>
            </div>
        </div>
        <div class="V_detail_box">
            <div class="search_">
                <div class="box">
                    <input type="text" v-model='keyword' placeholder="请输入内容">
                    <img src="@/assets/img_15.png" v-if="keyword==''" alt="">
                    <img src="@/assets/img_40.png" v-else class="cancel" alt="" @click="keyword='';ReGet();">
                </div>
                <div class="line_"></div>
                <span @click="ReGet">搜索</span>
            </div>

            <div class="Vote_list_img" v-if="detailInfo.type=='1'&&( mine!='mine' || (mine=='mine'&&detailInfo.is_view=='0'&&detailInfo.status!='30') )">
                <div class="vote_item" v-for="(el,index) in listInfo" :key="index">
                    <div class="Img_box" @click="DetailShow(index,1)">
                        <img :src="el.image" class="m_img" alt="">
                        <img src="@/assets/img_32.png" class="t_img" v-if="index===0" alt="">
                        <img src="@/assets/img_33.png" class="t_img" v-if="index===1" alt="">
                        <img src="@/assets/img_34.png" class="t_img" v-if="index===2" alt="">
                        <span class="t_tips" v-if="index>2">
                            <img src="@/assets/img_35.png" alt="">
                            {{index+1}}
                        </span>
                    </div>
                    <div class="content" @click="SelChange(el)">
                        <span v-if="!selList.includes(el.id)"></span>
                        <img v-else src="@/assets/img_27.png" alt="">
                        <p>
                            {{el.option_name}}
                        </p>
                    </div>
                </div>
            </div>

            <div class="Vote_list_txt"  v-if="detailInfo.type=='2'&&( mine!='mine' || (mine=='mine'&&detailInfo.is_view=='0'&&detailInfo.status!='30') )">
                <div class="vote_item" v-for="(el,index) in listInfo" :key="index">
                    <img src="@/assets/img_32.png" class="t_img" v-if="index===0" alt="">
                    <img src="@/assets/img_33.png" class="t_img" v-if="index===1" alt="">
                    <img src="@/assets/img_34.png" class="t_img" v-if="index===2" alt="">
                    <span class="t_tips" v-if="index>2">
                        {{index+1}}
                    </span>
                    <p @click="DetailShow(index,2)">
                        {{el.option_name}}
                    </p>
                    <span class="o_txt" v-if="!selList.includes(el.id)"  @click="SelChange(el)"></span>
                    <img v-else class="o_img" src="@/assets/img_27.png" alt=""  @click="SelChange(el)">
                </div>
            </div>

            <div class="Vote_list_img_rank"  v-if="detailInfo.type=='1'&&( (detailInfo.isVote&&detailInfo.is_view=='1') || detailInfo.status=='30' )&&mine=='mine'">
                <div class="vote_item" v-for="(el,index) in listInfo" :key="index">
                    <div class="Img_box" @click="DetailShow(index,1)">
                        <img :src="el.image" class="m_img" alt="">
                        <img src="@/assets/img_32.png" class="t_img" v-if="index===0" alt="">
                        <img src="@/assets/img_33.png" class="t_img" v-if="index===1" alt="">
                        <img src="@/assets/img_34.png" class="t_img" v-if="index===2" alt="">
                        <span class="t_tips" v-if="index>2">
                            <img src="@/assets/img_35.png" alt="">
                            {{index+1}}
                        </span>
                    </div>
                    <div class="content">
                        <h1>
                            {{el.option_name}}
                        </h1>
                        <div class="range">
                            <div class="m_box"><div class="trank" :style="`width:${el.range};`"></div></div>
                            <span>{{el.range}}</span>
                        </div>
                        <p>{{el.num}}</p>
                        <h2  v-if="selList.includes(el.id)">我的选择</h2>
                    </div>
                </div>
            </div>

            <div class="Vote_list_img_rank"  v-if="detailInfo.type=='2'&&( (detailInfo.isVote&&detailInfo.is_view=='1') || detailInfo.status=='30' )&&mine=='mine'">
                <div class="vote_item" v-for="(el,index) in listInfo" :key="index">
                    <div class="Img_box_1">
                        <img src="@/assets/img_32.png" class="t_img" v-if="index===0" alt="">
                        <img src="@/assets/img_33.png" class="t_img" v-if="index===1" alt="">
                        <img src="@/assets/img_34.png" class="t_img" v-if="index===2" alt="">
                        <span class="t_tips" v-if="index>2">
                            {{index+1}}
                        </span>
                    </div>
                    <div class="content" @click="DetailShow(index,2)">
                        <h1>
                            {{el.option_name}}
                        </h1>
                        <div class="range">
                            <div class="m_box"><div class="trank" :style="`width:${el.range};`"></div></div>
                            <span>{{el.range}}</span>
                        </div>
                        <p>{{el.num}}</p>
                        <h2  v-if="selList.includes(el.id)">我的选择</h2>
                    </div>
                </div>
            </div>
        </div>


        <div class="Cover_1" v-if="voteDetailShow"></div>
        <div class="vote_detail_box" v-if="voteDetailShow">
            <div class="content">
                <img :src="currentDetail.image" class="m_img" alt="" v-if="voteType===1">
                <p>
                    {{currentDetail.option_name}}
                </p>
            </div>
            <button @click="voteDetailShow=false">关闭</button>
        </div>  

        <button :class="['Start_btn',detailInfo.status=='10'?'active':'']" @click="VoteVote">
            {{detailInfo.status=='20'?'未开始':''}}
            {{detailInfo.status=='10'?(detailInfo.isVote?'已投票':'投票'):''}}
            {{detailInfo.status=='30'?'投票已结束':''}}
        </button>
    </div>
</template>

<script>
    import AllUrl from '../../utils/ipConfig'
    export default {
        data() {
            return {
                statusList:{
                    '0':'草稿',
                    '20':'未开始',
                    '10':'进行中',
                    '30':'已结束',
                },
                class_:{
                    '20':'S_1',
                    '10':'S_2',
                    '30':'S_3',
                },
                listInfo:[

                ],
                selList:[],
                voteDetailShow:false,
                currentDetail:{},
                voteType:1,
                detailInfo:{},
                keyword:'',
                page:1,
                total:0,
                pageSize:10,
                oldScrollTop:0,
                canGet:true,
                endCount:null,
                endTime:0,
                mine:'',

                clickControl:true,
            }
        },
        created() {
            this.mine = this.$route.query.mine || '';
            this.GetInfo();
        },
        computed: {
            currentInfo:function(){
                return this.$store.state.Info.voteInfo;
            },
            Countdown:function(){
                let time = this.GetTime(this.endTime);
                return time;
            },
        },
        beforeDestroy() {
            if(!!this.endCount){
                clearInterval(this.endCount);
                this.endCount = null;
            }
        },
        methods: {
            GetTime(time){
                let d = parseInt(time/(60*60*24));
                let h = parseInt((time%(60*60*24))/(60*60))>=10?parseInt((time%(60*60*24))/(60*60)):('0'+parseInt((time%(60*60*24))/(60*60)));
                let m = parseInt((time%(60*60*24))%(60*60)/60)>=10?parseInt((time%(60*60*24))%(60*60)/60):('0'+parseInt((time%(60*60*24))%(60*60)/60));
                let s = parseInt(time%60)>=10?parseInt(time%60):('0'+parseInt(time%60));
                return {d,h,m,s}
            },
            GetQueryString (name) {
                var url = window.location.href.split('?')[1];
                url = url[url.length-1];
                let localType = new URLSearchParams(url).get(name);
                return localType;
            },
            async GetInfo(){
                let id =  this.currentInfo.id;
                if(!!!id){
                    id = this.GetQueryString('id');
                }
                let detail = await this.Ajax.VoteDetail({id});
                let detailInfo = detail.data;
                if(detail.code == 402){
                    this.$message.warning('无权限查看！');
                    this.clickControl = true;
                    return false;
                }
                if(detail.code == 403){
                    this.$message.warning('尚未登录，将跳转登录请登录后查看！');
                    window.location.href = this.AllUrl.loginUrl + `?type=${this.$route.meta.type}&id=${id}`;
                    this.clickControl = true;
                    return false;
                }
                if(detail.code != 0){
                    this.$message.warning(detail.message);
                    this.clickControl = true;
                    return false;
                }
                detailInfo.start = window.GetTimeType(parseInt(detailInfo.start_time*1000),'yyyy.MM.dd');
                detailInfo.end = window.GetTimeType(parseInt(detailInfo.end_time*1000),'yyyy.MM.dd');
                detailInfo.image = JSON.parse(detailInfo.image);
                if(!!detailInfo.image&&detailInfo.image.length>0){
                    detailInfo.image = detailInfo.image[0];
                }else{
                    detailInfo.image = require('@/assets/img_14.png');
                }
                if(detailInfo.status == '10'){ // 进行中
                    let now = new Date();
                    let endTime = detailInfo.end_time*1000 - now.getTime();
                    this.endTime = endTime/1000;
                    this.endCount = setInterval(()=>{
                        if(this.endTime>0){
                            this.endTime--;
                        }else{
                            clearInterval(this.endCount);
                            // this.detailInfo.status = '30';
                        }
                    },1000)
                }
                if(!!detailInfo.voteRecords&&detailInfo.voteRecords.length>0){
                    detailInfo.isVote = true;
                    if((detailInfo.is_view == 1 || detailInfo.status=='30')&&this.mine=='mine'){
                        this.selList = [];
                        detailInfo.voteRecords.forEach(el => {
                            let l = JSON.parse(el.option_ids);
                            this.selList = this.selList.concat(l);
                        });
                        this.selList = this.selList.map(el=>{
                            return el + '';
                        })
                    }else{
                        this.selList = [];
                        let l = JSON.parse(detailInfo.voteRecords[detailInfo.voteRecords.length-1].option_ids);
                        this.selList = l.map(el=>{
                            return el + '';
                        })
                    }
                }else{
                    this.selList = [];
                    detailInfo.isVote = false;
                }
                console.log(this.selList);
                
                this.detailInfo = detailInfo;
                this.ReGet();
                this.$nextTick(()=>{
                    this.clickControl = true;
                    this.WixinSet();
                })
            },
            ReGet(){
                this.page = 1;
                this.total = 0;
                this.listInfo = [];
                this.GetOption();
            },
            async GetOption(){
                let info = {
                    vote_id:this.detailInfo.id,
                    keywords:'',
                    page:this.page,
                }
                console.log(info,'321');
                
                if(!!this.keyword){
                    info.keywords = this.keyword;
                }
                let option = await this.Ajax.VoteOption(info);
                this.total = parseInt(option.data.total);
                let list = option.data.data.map(el=>{
                    el.option_image = JSON.parse(el.option_image);
                    el.image = !!el.option_image[0]?el.option_image[0]:'';
                    el.check = false;
                    let ran = ((el.num / option.data.totalNum)*100).toFixed(2);
                    el.range =  ran + '%';
                    return el;
                })
                this.listInfo = this.listInfo.concat(list);
            },
            SelChange(el){
                if(this.detailInfo.status!='10'){
                    return false;
                }
                if(this.detailInfo.isVote && this.detailInfo.is_repetition == '0'){
                    return false;
                }
                let ind = this.selList.indexOf(el.id);
                if(ind > -1){
                    this.selList.splice(ind,1);
                }else{
                    if(this.detailInfo.option_type == '1'){ // 单选
                        this.selList = [el.id];
                    }
                    if(this.detailInfo.option_type == '2'){//多选
                        if(this.selList.length>=this.detailInfo.multiple_num){
                            this.selList.splice(0,1);
                            this.selList.push(el.id);
                        }else{
                            this.selList.push(el.id);
                        }
                    }
                }
            },
            DetailShow(index,type){
                this.voteType = type;
                this.currentDetail = this.listInfo[index];
                this.voteDetailShow = true;
            },
            Scroll(event){
                let direction = '';
                if(this.oldScrollTop > event.target.scrollTop){
                    direction = 'up';
                }else{
                    direction = 'down';
                }
                this.oldScrollTop = event.target.scrollTop;
                let scrollBottom = event.target.scrollHeight - event.target.scrollTop - event.target.clientHeight;
                if(direction === 'down' && scrollBottom < 100 && this.canGet && this.total > (this.page*this.pageSize)){
                    this.canGet = false;
                    this.page++;
                    this.GetOption();
                }
            },
            async VoteVote(){
                if(this.detailInfo.status!='10'){
                    return false;
                }
                if(this.detailInfo.isVote && this.detailInfo.is_repetition == '0'){
                    return false;
                }   
                let now = new Date();
                let endTime = this.detailInfo.end_time*1000 - now.getTime();
                if(endTime<=0){
                    this.$message.warning('已超过投票时间!');
                    return false;
                }
                
                if(!this.clickControl) return false;
                this.clickControl = false;
                let info = {
                    "vote_id": this.detailInfo.id,
                    option_ids:this.selList,
                }
                if(info.option_ids.length<=0){
                    this.$message.warning('请选择投票选项！');
                    this.clickControl = true;
                    return false;
                }
                let vote = await this.Ajax.VoteVote(info);
                if(vote.code == 0){
                    this.$message.success('投票成功！')
                }else{
                    this.$message.error(vote.message);
                }
                this.GetInfo();
            },
            async WixinSet(){
                let url = window.location.href;
                url = encodeURIComponent(url);
                let config = await this.Ajax.WxConfig({url});
                console.log(config);
                window.wx.config({
                    beta: true,
                    debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
                    ...config.data,
                    jsApiList: ['onMenuShareAppMessage','onMenuShareTimeline','showOptionMenu'] // 必填，需要使用的JS接口列表
                });
                window.wx.ready(()=>{
                    window.wx.showOptionMenu();
                    let img = !!this.detailInfo.Image&&this.detailInfo.Image.length>0?this.detailInfo.Image[0]:'';
                    let link = `${AllUrl.baseIp}/html/index.html#/vote/detail?type=vote&id=${this.detailInfo.id}`;
                    window.wx.onMenuShareAppMessage({
                        title: this.detailInfo.title, // 分享标题
                        desc: this.detailInfo.title, // 分享描述
                        link: link, // 分享链接
                        imgUrl: img, // 分享图标
                        success: function () {
                            // 用户确认分享后执行的回调函数
                            console.log('success');
                        },
                        cancel: function () {
                            // 用户取消分享后执行的回调函数
                            console.log('cancel');
                        }
                    })
                    window.wx.onMenuShareTimeline({
                        title: this.detailInfo.title, // 分享标题
                        link: link, // 分享链接
                        imgUrl: img, // 分享图标
                        success: function () {
                            // 用户确认分享后执行的回调函数
                            console.log('success');
                        },
                        cancel: function () {
                            // 用户取消分享后执行的回调函数
                            console.log('cancel');
                        }
                    })
                })  
            },
        },
    }
</script>

<style lang="scss" scoped>

</style>