<template>
    <div class="Exam_result" v-show="complete">
        <div class="Exam_result_info_1" v-if="!!currentInfo.status_&&currentInfo.status_==6">
            <img src="@/assets/img_25.png" alt="">
            <h1>
                阅卷中，请耐心等待测试结果
            </h1>
        </div>
        <div class="Exam_result_info"  v-if="!!currentInfo.examInfo&&currentInfo.status_!=6">
            <img src="@/assets/img_23.png" v-if="currentInfo.status_==5" alt="">
            <img src="@/assets/img_24.png" v-else alt="">
            <h1>
                {{currentInfo.status_==5?'本次考试已通过':''}}
                {{currentInfo.status_==4?'本次考试未通过':''}}
            </h1>
            <h2>
                {{currentInfo.examInfo.grade}}<span>分</span>
            </h2>
            <h3>
                共{{currentInfo.total_items}}题，答对{{currentInfo.examInfo.right_items}}题，用时{{currentInfo.examInfo.total_time}}分钟
            </h3>
            <button @click="See">查看答题详情</button>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                complete:false,
            }
        },
        computed: {
            currentInfo:function(){
                return this.$store.state.Info.examInfo;
            },
        },
        mounted() {
            this.GetResult();
        },
        methods: {
            See(){
                sessionStorage.setItem('TestType','SeeDetail');
                this.$router.push({path:'/exam/test?type=SeeDetail'});
            },
            async GetResult(){
                this.complete = false;
                let id = this.currentInfo.id;
                if(!!!id){
                    id = this.GetQueryString('id');
                }
                let result = await this.Ajax.ExamResult({paper_id:id});
                if(result.code == 403){
                    this.$message.warning('尚未登录，将跳转登录请登录后查看！');
                    let url = this.AllUrl.loginUrl + `?type=${this.$route.meta.type}&id=${id}`;
                    window.location.href = url;
                    return false;
                }
                let examInfo = null;
                if(result.code != 0){
                    this.$message.warning(result.message);
                    return false;
                }
                let list = result.data.map(el=>{
                    if(!!!examInfo) examInfo = el.examInfo;
                    el.Score = el.myAnswer.grade;
                    if(el.item_type == 1 || el.item_type == 2){
                        el.item_option = JSON.parse(el.item_option);
                        el.option = [];
                        for (const i in el.item_option) {
                            el.option.push(el.item_option[i]);
                        }
                        if(el.item_type == 1){
                            el.answer = [];
                            if(!!el.myAnswer.answer){
                                el.answer = JSON.parse(el.myAnswer.answer);
                            }
                            el.right = JSON.parse(el.item_answer);
                        }else{
                            el.answer = JSON.parse(el.myAnswer.answer);
                            el.right = JSON.parse(el.item_answer);
                        }
                    }else if(el.item_type == 3){
                        el.option = ['正确','错误'];
                        if(!!el.myAnswer.answer){
                            el.answer = [];
                            if(JSON.parse(el.myAnswer.answer) == '对'){
                                el.answer = 'A';
                            }else if(JSON.parse(el.myAnswer.answer) == '错'){
                                el.answer = 'B';
                            }else{
                                el.answer = '';
                            }
                        }else{
                            el.answer = '';
                        }
                        if(JSON.parse(el.item_answer) == '对'){
                            el.right = 'A';
                        }else{
                            el.right = 'B';
                        }
                    }else{
                        el.answer = el.myAnswer.answer;
                        el.right = el.item_answer
                    }
                    return el;
                })
                let item = JSON.parse(JSON.stringify(this.currentInfo));
                item.examInfo = examInfo;
                item.id = id;
                if(item.examInfo.is_join == '1' ){
                    let l = [4,5,6];
                    item.status_ = l[item.examInfo.status];
                }
                this.$store.commit('setValue',{key:'examInfo',value:item});
                this.$store.commit('setValue',{key:'testInfo',value:list});
                this.complete = true;
            },
        },
    }
</script>

