<template>
    <div class="Exam_test">
        <h1 class="title">
            <img :src='imgList[questionList[currentIndex].item_type-1]' alt="">
            {{currentIndex+1}}.{{questionList[currentIndex].item_title}} ({{questionList[currentIndex].item_grade}}分)
        </h1>

        <div class="option_box" v-if="questionList[currentIndex].item_type==1||questionList[currentIndex].item_type==2||questionList[currentIndex].item_type==3">
            <div v-for="(el,index) in questionList[currentIndex].option" :class="['option_item',
                (type!='SeeDetail'&&questionList[currentIndex].answer==Letter[index]&&(questionList[currentIndex].item_type==1||questionList[currentIndex].item_type==3))?'active':'',
                (type!='SeeDetail'&&!!questionList[currentIndex].answer&&questionList[currentIndex].answer.includes(Letter[index])&&questionList[currentIndex].item_type==2)?'active':'',
                (type=='SeeDetail'&&((questionList[currentIndex].item_type==1||questionList[currentIndex].item_type==3)&&Letter[index]==questionList[currentIndex].right))?'active':'',
                (type=='SeeDetail'&&((questionList[currentIndex].item_type==1||questionList[currentIndex].item_type==3)&&Letter[index]==questionList[currentIndex].answer&&questionList[currentIndex].answer!=questionList[currentIndex].right))?'error':'',
                (type=='SeeDetail'&&(questionList[currentIndex].item_type==2&&!!questionList[currentIndex].right&&questionList[currentIndex].right.includes(Letter[index])))?'active':'',
            ]" :key="index" @click='SelOption(index)'>
                
                <img src="@/assets/img_27.png" v-if="type=='SeeDetail'&&((questionList[currentIndex].item_type==1||questionList[currentIndex].item_type==3)&&Letter[index]==questionList[currentIndex].right)" alt="">
                <img src="@/assets/img_26.png" v-if="type=='SeeDetail'&&((questionList[currentIndex].item_type==1||questionList[currentIndex].item_type==3)&&Letter[index]==questionList[currentIndex].answer&&questionList[currentIndex].answer!=questionList[currentIndex].right)" alt="">

                <img src="@/assets/img_27.png" v-if="type=='SeeDetail'&&(questionList[currentIndex].item_type==2&&!!questionList[currentIndex].right&&questionList[currentIndex].right.includes(Letter[index]))" />
                <img src='@/assets/img_26.png' v-if="type=='SeeDetail'&&(questionList[currentIndex].item_type==2&&!!questionList[currentIndex].right&&!questionList[currentIndex].right.includes(Letter[index])&&!!questionList[currentIndex].answer&&questionList[currentIndex].answer.includes(Letter[index]))" />

                <span v-if="
                    type!='SeeDetail'||(type=='SeeDetail'&&(Letter[index]!=questionList[currentIndex].right&&Letter[index]!=questionList[currentIndex].answer&&(questionList[currentIndex].item_type==1||questionList[currentIndex].item_type==3)))||(type=='SeeDetail'&&(questionList[currentIndex].item_type==2&&!!questionList[currentIndex].right&&!questionList[currentIndex].right.includes(Letter[index])&&!!questionList[currentIndex].answer.includes&&!questionList[currentIndex].answer.includes(Letter[index])))
                    ">
                    {{Letter[index]}}
                </span>
                <p>
                    {{el}}
                </p>
            </div>
        </div>
        <div class="option_box" v-if="questionList[currentIndex].item_type==4 && type!='SeeDetail'">
            <textarea name="" v-model="questionList[currentIndex].answer" id="" maxlength="500" cols="30" placeholder="请输入答案" rows="10" @blur="Blur"></textarea>
        </div>

        <div class="Answer_jdt" v-if="questionList[currentIndex].item_type==4 && type=='SeeDetail'">
            <h1>
                我的答案
            </h1>
            <p v-html="questionList[currentIndex].answer.replace(/(\\r\\n)|(\\n)/g,'<br>')">
            </p>
            <div class="one">
                <h1>
                    <img src="@/assets/img_28.png" alt=""> 正确答案
                </h1>
                <p v-html="questionList[currentIndex].right.replace(/(\\r\\n)|(\\n)/g,'<br>')">
                </p>
            </div>
        </div>

        <div class="Answer" v-if="questionList[currentIndex].item_type!=4 && type=='SeeDetail'">
            <span class="mine">
                我的答案  {{questionList[currentIndex].item_type==2?questionList[currentIndex].answer.join(''):questionList[currentIndex].answer}}
            </span>
            <img src="@/assets/img_28.png" alt="">
            <span>
                正确答案 {{questionList[currentIndex].item_type==2?questionList[currentIndex].right.join(''):questionList[currentIndex].right}}
            </span>
        </div>
        <div class="Score" v-if="type=='SeeDetail'">
            本题得分：<span>{{questionList[currentIndex].Score}}分</span>
        </div>

        <div :class="['Bottom_info',type=='SeeDetail'?'See_':'']">
            <img src="@/assets/img_18.png" alt="">
            <span> <em>{{currentIndex+1}}</em>/{{questionList.length}} </span>
            <label>{{duration}}</label>
            <button @click="Next()">提交</button>
        </div>


        <div class="C_cover" v-show="CancelShow"></div>
        <div class="Cancel_box" v-if="CancelShow">
            <h1>
                提示
            </h1>
            <h2>
                你有未答完的题，确认提交吗？
            </h2>
            <div>
                <button class="btn_cancel" style="margin-right:.55rem;" @click="CancelShow=false;">取消</button>
                <button class="btn_confirm" @click="Updata()">确定</button>
            </div>
        </div>

        <div class="Swtich_btn">
            <div class="" @click="QuestionChange(-1)"> 
                <img src="@/assets/img_53.png" v-if="currentIndex==0" alt=""> 
                <img src="@/assets/img_54.png" class="ra" v-else alt=""> 
            </div>
            <div class="" @click="QuestionChange(1)"> 
                <img src="@/assets/img_53.png" class="ra" v-if="currentIndex==(questionList.length-1)" alt=""> 
                <img src="@/assets/img_54.png" v-else alt=""> 
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                imgList:[
                    require('../../assets/img_19.png'),
                    require('../../assets/img_20.png'),
                    require('../../assets/img_21.png'),
                    require('../../assets/img_22.png'),
                ],
                currentIndex:0,
                Letter:['A','B','C','D','E','F','G','H','I','J','k'],
                oldLeft:0,
                changeControl:true,
                type:'',
                durationTime:0,
                start:'',
                CancelShow:false,

                canContinue:true,
            }
        },
        created() {
            if(!!this.$route.query.type){
                this.type = this.$route.query.type;
            }
            if(this.type == 'SeeDetail'){
                window.document.title = '试卷详情';
            }else{
                this.durationTime = this.currentInfo.duration_time * 60;
                this.countTime = setInterval(this.CTime,1000);
                let start = new Date();
                this.start = start.getTime() / 1000;
            }
        },
        mounted() {

        },
        computed: {
            currentInfo:function(){
                return this.$store.state.Info.examInfo;
            },
            questionList:function(){
                return this.$store.state.Info.testInfo;
            },
            duration:function(){
                return window.getTime(this.durationTime);
            }
        },
        methods: {
            Blur(){
                window.scrollTo(0,0);
            },
            CTime(){
                this.durationTime--;
                if(this.durationTime<=0){
                    clearInterval(this.countTime);
                    this.countTime = null;
                    this.Updata('over');
                }
            },
            SelOption(index){
                if(this.type == 'SeeDetail'){
                    return false;
                }
                if(this.questionList[this.currentIndex].item_type == 1||this.questionList[this.currentIndex].item_type == 3){
                    this.questionList[this.currentIndex].answer = this.Letter[index];
                }
                if(this.questionList[this.currentIndex].item_type == 2){
                    if(!this.questionList[this.currentIndex].answer.includes(this.Letter[index])){
                        this.questionList[this.currentIndex].answer.push(this.Letter[index]);
                    }else{
                        let ind = this.questionList[this.currentIndex].answer.indexOf(this.Letter[index]);
                        this.questionList[this.currentIndex].answer.splice(ind,1);
                    }
                }
            },
            QuestionChange(type){
                if(type == -1){
                    if(this.currentIndex>0){
                        this.currentIndex--;
                    }
                }else{
                    // if((this.questionList[this.currentIndex].item_type == 1||this.questionList[this.currentIndex].item_type == 3) && this.questionList[this.currentIndex].answer == ''){
                    //     this.$message.warning('请选择答案！');
                    //     return false;
                    // }
                    // if(this.questionList[this.currentIndex].item_type == 2 && this.questionList[this.currentIndex].answer.length<=0){
                    //     this.$message.warning('请选择答案！');
                    //     return false;
                    // }
                    // if(this.questionList[this.currentIndex].item_type == 4 && this.questionList[this.currentIndex].answer == ''){
                    //     this.$message.warning('请输入答案!');
                    //     return false;
                    // }
                    if(this.currentIndex<(this.questionList.length-1)){
                        this.currentIndex++;
                    }
                }
            },
            Next(type){
                if(type == 'over'){
                    this.Updata();
                }else{
                    let pan = false;
                    this.questionList.forEach(el=>{
                        if((el.item_type == 1||el.item_type == 3) && el.answer == ''){
                            pan = true;
                        }
                        if(el.item_type == 2 && el.answer.length<=0){
                            pan = true;
                        }
                        if(el.item_type == 4 && el.answer == ''){
                            pan = true;
                        }
                    })
                    if(pan){
                        this.CancelShow = true;
                    }else{
                        this.Updata();
                    }
                }
            },
            async Updata(){
                if(!this.canContinue) return false;

                this.canContinue = false;
                let end = new Date();
                end = end.getTime() / 1000;
                let s = this.currentInfo.duration_time -  Math.floor(this.durationTime / 60);
                let info = {
                    paper_id:this.currentInfo.id,
                    total_time:s,
                    start_time:this.start,
                    end_time:end,
                    answer:[],
                }
                info.answer = this.questionList.map(el=>{
                    let item = {
                        paper_id:this.currentInfo.id,
                        examination_item_id:el.id,
                        answer:'',
                    };
                    if(el.item_type == 1){
                        item.answer = !!el.answer[0]?el.answer[0]:'';
                    }
                    if(el.item_type == 2){
                        item.answer = el.answer;
                    }
                    if(el.item_type == 3){
                        if(!!el.answer[0]){
                            item.answer = el.answer[0] == 'A'?'对':'错';
                        }else{
                            item.answer = '';
                        }
                    }
                    if(el.item_type == 4){
                        item.answer = el.answer;  
                    }
                    return item;
                })
                let up = await this.Ajax.ExamAnswer(info);
                sessionStorage.setItem('questionList',JSON.stringify(this.questionList));
                sessionStorage.setItem('examResult','1');
                this.$router.push({path:'/exam/result'});
                this.canContinue = true;
            },
            touchstart(e){
                this.oldLeft = e.touches[0].pageX;
            },
            touchmove(e){
                let end = e.touches[0].pageX;
                if((end - this.oldLeft)>150){
                    if(!this.changeControl){
                        return false;
                    }
                    this.changeControl = false;
                    setTimeout(()=>{
                        this.changeControl = true;
                    },1500)
                    console.log('left to right');
                    this.QuestionChange(-1);
                }
                if((end - this.oldLeft)<-150){
                    if(!this.changeControl){
                        return false;
                    }
                    this.changeControl = false;
                    setTimeout(()=>{
                        this.changeControl = true;
                    },1500)
                    console.log('right to left');
                    this.QuestionChange(1);
                }
            }
        },
        beforeDestroy() {
            if(!!this.countTime){
                clearInterval(this.countTime);
            }
        },
    }
</script>

