<template>
    <div class="Content_box" style="padding-bottom:0;"  @scroll="Scroll">
        <div class='Home_head'>
            <div class="Search_box">
                <div class="box">
                    <input type="text"  v-model="keywords" placeholder="搜索活动标题">
                    <img src="@/assets/img_15.png" alt="" @click="ReGet">
                </div>
            </div>
            <div class="tabs_">
                <span v-for='(el,index) in tab_list' :key="index" :class="index===active?'active':''" @click="tabChange(index)">
                    {{el}}
                </span>
            </div>
        </div>

        <div class="List_box" v-if="dataList.length>0">
            <ActivityItem v-for="(el,index) in dataList" :key="index" :Item='el' @Detail='Detail' />
        </div>
        <NoData v-else />
    </div>
</template>

<script>
    export default {
        data(){
            return{
                active:0,
                tab_list:['全部','未参加','已参加'],
                type_list:['','0','1'],
                dataList:[],
                pageSize:10,
                pageNumber:1,
                total:0,
                keywords:'',
                startTime:'',
                endTime:'',
                oldScrollTop:0,
                canGet:true,
            }
        },
        created() {
            this.GetList();
        },
        methods: {
            tabChange(index){
                this.active = index;
                this.ReGet();
            },
            ReGet(){
                this.total = 0;
                this.pageNumber = 1;
                this.dataList = [];
                this.GetList();
            },
            Detail(el){
                this.$store.commit('setValue',{key:'activityInfo',value:el});
                this.$router.push({path:'/activity/detail'});
            },
            async GetList(){
                let info = {
                    page:this.pageNumber,
                    join:this.type_list[this.active],
                }
                let data = await this.Ajax.ActivityList(info);
                let list = data.data.data.map(el=>{
                    el.startTime = window.GetTimeType(parseInt(el.start_time*1000),'yyyy.MM.dd hh:mm:ss');
                    el.endTime = window.GetTimeType(parseInt(el.end_time*1000),'yyyy.MM.dd hh:mm:ss');
                    el.image = JSON.parse(el.image);
                    if(!!el.image&&el.image.length>0){
                        el.image = el.image[0];
                    }else{
                        el.image = require('@/assets/img_14.png');
                    }
                    return el;
                })
                this.total = parseInt(data.data.total);
                this.dataList = this.dataList.concat(list);
                this.$nextTick(()=>{
                    this.canGet = true;
                })
            },
            Scroll(event){
                let direction = '';
                if(this.oldScrollTop > event.target.scrollTop){
                    direction = 'up';
                }else{
                    direction = 'down';
                }
                this.oldScrollTop = event.target.scrollTop;
                let scrollBottom = event.target.scrollHeight - event.target.scrollTop - event.target.clientHeight;
                if(direction === 'down' && scrollBottom < 100 && this.canGet && this.total > (this.pageNumber*this.pageSize)){
                    this.canGet = false;
                    this.pageNumber++;
                    this.GetList();
                }
            },
        },
    }
</script>

