<template>
    <div class="V_item" @click="Detail">
            <div class="I_box">
                <img :src="Item.image" class="m_img" alt="">
                <span :class="class_[Item.status]">
                    {{statusList[Item.status]}}
                </span>
            </div>
            <h1 class="title">
                {{Item.title}}
            </h1>
            <h2 v-if="Home!='home'">
                发起人：{{Item.author_name}}
                <span>{{Item.vote_num}}人参与</span>
            </h2>
            <h2>
                投票时间：{{Item.start}}-{{Item.end}}
                <span v-if="Home=='home'" :class="Item.status=='10'?'move':''">{{Item.vote_num}}人参与</span>
            </h2>
            <button :class="!Item.isVote?'active':''" v-if="Item.status=='10'">
                {{Item.isVote?'已投票':'投票'}}
            </button>
        </div>
</template>

<script>
    export default {
        props:['Item','Home'],
        data() {
            return {
                statusList:{
                    '0':'草稿',
                    '20':'未开始',
                    '10':'进行中',
                    '30':'已结束',
                },
                class_:{
                    '20':'S_1',
                    '10':'S_2',
                    '30':'S_3',
                },
            }
        },
        methods: {
            Detail(){
                this.$emit('Detail',this.Item);
            },
        },
    }
</script>

