const state = {
    num: 0,
    messageInfo:{},
    voteInfo:{},
    schoolInfo:{},
    examInfo:{},
    activityInfo:{},
    testInfo:{},
    HomeActive:0,
    userInfo:{},// 用户信息
};

const mutations = {
    setNum(state, n){
        state.num += n
    },
    setValue(state,v){
        state[v.key] = JSON.parse(JSON.stringify(v.value));
    }
}

const actions = {
    setNums({ commit }, n) {
        commit('setNum', n);
    }
};

export default{
    state,
    mutations,
    actions
}