<template>
    <div class="Content_box" style="padding-bottom:0;"  @scroll="Scroll">
        <div class='Home_head'>
            <div class="Search_box">
                <div class="box">
                    <input type="text" v-model="keywords" placeholder="请输入搜索内容">
                    <img src="@/assets/img_15.png" v-if="keywords===''"    alt="">
                    <img src="@/assets/img_40.png" v-else class="cancel" alt="" @click="keywords=''">
                </div>
                <span @click="Goto('/home')">
                    取消
                </span>
            </div>
            <div class="tabs_">
                <span v-for='(el,index) in tab_list' :key="index" :class="index===active?'active':''" @click="tabChange(index)">
                    {{el}}
                </span>
            </div>
        </div>
        <component v-show="Show" ref="Target" :is="tab_list_[active]" :canGet.sync='canGet' :keywords='keywords' @ChangeCan='ChangeCan' />

        <div class="No_data" v-show="!Show">
            <img src="@/assets/img_16.png" alt="">
            <span>
                暂无数据
            </span>
        </div>
    </div>
</template>

<script>
    import Examination from './components/examination'
    import Vote from './components/vote'
    import School from './components/School'
    import Message from './components/message'
    import Activity from './components/activity.vue'
    export default {
        components:{Examination,Vote,School,Message,Activity},
        data(){
            return{
                active:0,
                tab_list:['考试','投票','学堂','酷讯','报名'],
                tab_list_:['Examination','Vote','School','Message','Activity'],
                keywords:'',
                oldScrollTop:0,
                canGet:true,
                Show:true,
            }
        },
        methods: {
            ChangeCan(l){
                console.log(l);
                if(l>0){
                    this.Show = true;
                }else{
                    this.Show = false;
                }
            },
            Goto(path){
                this.$router.push(path);
            },
            tabChange(index){
                this.active = index;
            },
            Scroll(event){
                let direction = '';
                if(this.oldScrollTop > event.target.scrollTop){
                    direction = 'up';
                }else{
                    direction = 'down';
                }
                this.oldScrollTop = event.target.scrollTop;
                let scrollBottom = event.target.scrollHeight - event.target.scrollTop - event.target.clientHeight;
                if(direction === 'down' && scrollBottom < 100 && this.canGet){
                    this.canGet = false;
                    this.$refs['Target'].ScrollGet();
                }
            },
        },
    }
</script>

