<template>
    <div id="app">
        <router-view />
        <!-- <p>
            {{token}}
        </p>
        <p>
            {{user}}
        </p>
        <p>
            {{userInfo}}
        </p> -->
    </div>
</template>

<script>
    export default {
        name: 'App',
        data() {
            return {
                code:'',
                token:'',
                user:'',
                userInfo:'',
                type:'',
            }
        },
        created() {
            if(this.$route.path === '/'){
                console.log(123);
                this.BaseSet();
            }
        },
        mounted() {
            
        },
        watch: {
            '$route'(to, from){
                let can = ['/vote/detail','/school/detail','/message/detail','/activity/detail'];
                if(!can.includes(to.fullPath)){
                    this.SetWx();
                }
            }
        },
        methods: {
            GetQueryString (name) {
                let url = window.location.href.split('?');
                url = url[url.length-1];
                let localType = new URLSearchParams(url).get(name);
                return localType;
            },
            async BaseSet(){
                let info = await this.Ajax.UserInfo({});
                let userInfo = info.data;
                this.$store.commit('setValue',{key:'userInfo',value:userInfo});
                sessionStorage.setItem('userInfo',JSON.stringify(userInfo));
                let type = this.GetQueryString('type');
                let id = this.GetQueryString('id');
                let item = {id};
                
                if(!!type){
                    sessionStorage.setItem('YType',type);
                    sessionStorage.setItem('YId',id);
                }else{
                    sessionStorage.setItem('YType','');
                    sessionStorage.setItem('YId','');
                }
                if(type == 'activity'){
                    this.$store.commit('setValue',{key:'activityInfo',value:item});
                    window.location.href = this.AllUrl.pageUrl + '#/activity/detail';
                }else if(type == 'examination'){
                    let detail = await this.Ajax.ExamineInfo({id:id});
                    let el = detail.data;
                    let now = new Date();
                    now = now.getTime();
                    el.startTime = window.GetTimeType(parseInt(el.start_time*1000),'yyyy.MM.dd hh:mm:ss');
                    el.endTime = window.GetTimeType(parseInt(el.end_time*1000),'yyyy.MM.dd hh:mm:ss');
                    if(now < el.start_time*1000){ // 未开始 
                        el.status_ = 1;
                    }else if(now > el.end_time*1000){// 已结束
                        el.status_ = 2;
                    }else{
                        el.status_ = 3;
                    }
                    if(el.examInfo.is_join == '1' ){
                        let l = [4,5,6];
                        el.status_ = l[el.examInfo.status];
                    }
                    this.$store.commit('setValue',{key:'examInfo',value:el});
                    if(el.status_ == 4 || el.status_ == 5 || el.status_ == 6){
                        window.location.href = this.AllUrl.pageUrl + '#/exam/result';
                    }else{
                        window.location.href = this.AllUrl.pageUrl + '#/exam/info';
                    }
                }else if(type == 'message'){
                    this.$store.commit('setValue',{key:'messageInfo',value:item});
                    window.location.href = this.AllUrl.pageUrl + '#/message/detail';
                }else if(type == 'school'){
                    this.$store.commit('setValue',{key:'schoolInfo',value:item});
                    window.location.href = this.AllUrl.pageUrl + '#/school/detail';
                }else if(type == 'vote'){
                    this.$store.commit('setValue',{key:'voteInfo',value:item});
                    window.location.href = this.AllUrl.pageUrl + '#/vote/detail';
                }else{
                    window.location.href = this.AllUrl.pageUrl + '#/home';
                }
            },
            async SetWx(){
                let url = window.location.href.split('#')[0];
                url = encodeURIComponent(url);
                let config = await this.Ajax.WxConfig({url});
                window.wx.config({
                    beta: true,
                    debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
                    ...config.data,
                    jsApiList: ['hideOptionMenu'] // 必填，需要使用的JS接口列表
                });
                window.wx.ready(()=>{
                    window.wx.hideOptionMenu();
                })
            },
        },
    }
</script>

