<template>
    <div v-show="show" class="Date_box">
        <div class="Date_content">
            <div class="Date_sel">
                <div :class="['one',active===0?'active':'']" @click="ChangeTime(0)">
                    <h1>开始时间</h1>
                    <h2>
                        {{start.year}}-{{start.month}}-{{start.day}}
                    </h2>
                </div>
                <span>至</span>
                <div :class="['one',active===1?'active':'']" @click="ChangeTime(1)">
                    <h1>结束时间</h1>
                    <h2>
                        {{end.year}}-{{end.month}}-{{end.day}}
                    </h2>
                </div>
            </div>

            <div class="Date_sel_time ui-datetime">
                
            </div>

            <div class="btn_">
                <button @click="Cancel">
                    取消
                </button>
                <button @click="Comfirm">
                    完成
                </button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        props:['show'],
        data() {
            return {
                active:0,
                time:{
                    year:[],
                    month:[],
                    day:[],
                },
                start:{
                    year:'',month:'',day:'',
                },
                end:{
                    year:'',month:'',day:'',
                },
                current:{
                    year:'',month:'',day:'',
                },
                currentIndex:null,
                allTop:0,
                oldTop:0,
                Info:{
                    type:'diy',
                    lineHeight: 30,
                    data:[
                        {
                            key: 'day',
                            resource: [],
                            value: "",
                            unit: '',
                        },
                        {
                            key: 'hour',
                            resource: [],
                            value: "",
                            unit: '',
                        },
                        {
                            key: 'minute',
                            resource: [],
                            value: "",
                            unit: '',
                        },
                    ],
                },
                target:null,
                first:true,
            }
        },
        created() {
            
        },  
        mounted() {
        },
        watch: {
            show:function(v){
                if(v&&this.first){
                    this.first = false;
                    this.BaseSet();
                }
            },
        },  
        methods: {
            BaseSet(){
                let now = new Date();
                this.start.year = now.getFullYear();
                this.start.month = (now.getMonth()+1)>10?(now.getMonth()+1):('0'+(now.getMonth()+1));
                this.start.day = now.getDate()>10?now.getDate():('0'+(now.getDate()));
                let yearList = [];
                let year = now.getFullYear() - 10;
                while (year<(now.getFullYear()+10)) {
                    yearList.push(`${year}年`);
                    year++;
                }
                let monthList = [];
                let month = 1;
                while (month<13) {
                    monthList.push(`${month}月`);
                    month++;
                }
                this.current = JSON.parse(JSON.stringify(this.start));
                this.SetDay();
                this.time.year = yearList;
                this.time.month = monthList;
                this.end = JSON.parse(JSON.stringify(this.start));

                this.Info.data[0].resource = yearList;
                this.Info.data[0].value = `${this.start.year}年`;

                this.Info.data[1].resource = monthList;
                this.Info.data[1].value = `${now.getMonth()+1}月`;
                this.Info.data[2].value = `${now.getDate()}日`;
                this.Info.onChange = this.DateChange;
                document.addEventListener('touchmove', function (e) { e.preventDefault(); }, this.isPassive() ? {
                    capture: false,
                    passive: false
                } : false);
                setTimeout(()=>{
                    let tar = document.querySelector('.Date_sel_time');
                    this.target = new window.DateTime(tar,this.Info);
                    this.target.init();
                },100)
            },
            isPassive() {
                var supportsPassiveOption = false;
                try {
                    addEventListener("test", null, Object.defineProperty({}, 'passive', {
                        get: function () {
                            supportsPassiveOption = true;
                        }
                    }));
                } catch(e) {}
                return supportsPassiveOption;
            },
            ChangeTime(index){
                this.active = index;
                if(index === 0){
                    this.current = JSON.parse(JSON.stringify(this.start));
                }else{
                    this.current = JSON.parse(JSON.stringify(this.end));
                }
                this.Info.data[0].value = `${this.current.year}年`;
                this.Info.data[1].value = `${parseInt(this.current.month)}月`;
                this.Info.data[2].value = `${parseInt(this.current.day)}日`;
                this.SetDay(1);
            },
            DateChange(data){
                console.log(data);
                let data_ = JSON.parse(JSON.stringify(data))
                let {day,hour,minute} = data_;
                this.Info.data[0].value = day;
                this.Info.data[1].value = hour;
                this.Info.data[2].value = minute;

                let year = day.replace('年','');
                let month = hour.replace('月','');
                let day_ = minute.replace('日','');
                month = month>=10?month:(`0${month}`);
                day_ = day_>=10?day_:(`0${day_}`);
                let pan = false;
                if(month != this.current.month){
                    pan = true;
                }
                this.current.year = year;
                this.current.month = month;
                this.current.day = day_;
                if(this.active === 0){
                    this.start = JSON.parse(JSON.stringify(this.current));
                }else{
                    this.end = JSON.parse(JSON.stringify(this.current));
                }
                if(pan){
                    this.SetDay(1);
                }
            },
            SetDay(type){
                let day = new Date(this.current.year,(parseInt(this.current.month)),0).getDate();
                let dayList = [];
                let day_ = 1;
                while (day_<=day) {
                    dayList.push(`${day_}日`);
                    day_++;
                }
                if(this.current.day>day){
                    this.current.day = '01';
                    if(this.active === 0){
                        this.start.day = '01';
                    }else{
                        this.end.day = '01';
                    }
                    this.Info.data[2].value = '1日';
                }
                
                this.time.day = dayList;
                this.Info.data[2].resource = dayList;

                if(type===1){
                    let tar = document.querySelector('.Date_sel_time');
                    this.target = new window.DateTime(tar,this.Info);
                    this.target.init();
                }
            },
            Cancel(){
                this.$emit('update:show',false);
            },
            Comfirm(){
                let start = new Date(`${this.start.year}-${this.start.month}-${this.start.day}`);
                let end = new Date(`${this.end.year}-${this.end.month}-${this.end.day}`);
                if(start.getTime() > end.getTime()){
                    this.$message.warning('开始时间不能晚于结束时间！');
                    return false;
                }
                let time = {
                    start:window.GetTimeType(start,'yyyy-MM-dd'),
                    end:window.GetTimeType(end,'yyyy-MM-dd'),
                }
                this.$emit('Comfirm',time);
            },
        },
        mounted() {
            let box = document.querySelector('.Date_sel_time');
            let box1 = document.querySelector('.Date_sel_time');
            this.allTop = box.offsetTop;
            console.log(this.allTop);
        },
    }
</script>

