<template>
    <div class="V_item" @click="Detail">
        <div class="I_box">
            <img :src="Item.image" class="m_img" alt="">
            <span :class="class_[Item.status]">
                {{statusList[Item.status]}}
            </span>
        </div>
        <h1 class="title">
            {{Item.title}}
        </h1>
        <h2>
            主题：{{Item.theme}}
        </h2>
        <h2>
            活动时间：{{Item.startTime}}-{{Item.endTime}}
        </h2>
        <h2>
            活动地点：{{Item.address}}
        </h2>
        <h2 v-if="Home!='home'">
            发起人：{{Item.author_name}}
        </h2>
        <button class="active" v-if="Item.status=='10'">
            {{Item.apply===null?'报名':'取消报名'}}
        </button>
    </div>
</template>

<script>
    export default {
        props:['Item','Home'],
        data() {
            return {
                statusList:{
                    '10':'未开始',
                    '20':'进行中',
                    '30':'已结束',
                },
                class_:{
                    '10':'S_1',
                    '20':'S_2',
                    '30':'S_3',
                },
            }
        },
        methods: {
            Detail(){
                this.$emit('Detail',this.Item);
            },
        },
    }
</script>

