import Vue from 'vue'


import App from './App.vue'


import router from './router'
import store from './store'
import './style/common.scss'
import './style/style.scss'
import './utils/common'
import AllUrl from './utils/ipConfig'
import Ajax from './ajaxApi/AjaxApi'

import ECharts from 'vue-echarts'
Vue.component('v-chart', ECharts)

import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(ElementUI);

require('./mixin.js')

import ExamItem from './components/ExamItem'
Vue.component('ExamItem',ExamItem);
import ActivityItem from './components/ActivityItem'
Vue.component('ActivityItem',ActivityItem);
import MessageItem from './components/MessageItem'
Vue.component('MessageItem',MessageItem);
import SchoolItem from './components/SchoolItem'
Vue.component('SchoolItem',SchoolItem);
import VoteItem from './components/VoteItem'
Vue.component('VoteItem',VoteItem);

import NoData from './components/Nodata/index.vue'
Vue.component('NoData',NoData);

Vue.prototype.Ajax = Ajax;
Vue.prototype.AllUrl = AllUrl;



router.beforeEach((to, from, next) => {
    if(from.fullPath == '/exam/result' && to.fullPath == '/exam/test'){
        next('/home')
    }else if(from.fullPath == '/home' && to.fullPath == '/exam/test'){
        next('/home')
    }else{
        window.document.title = to.meta.title || '';
        next()
    }
    // window.document.title = to.meta.title || '';
    // next()
})
Vue.config.productionTip = false
new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#app')