window.onresize  = function(){
    let c = document.documentElement.clientWidth;
    if (c > 1080) {
        c = 1080;
    }
    document.documentElement.style.fontSize = c / 7.5 + "px";
}

window.getTime = (time)=>{
    if(typeof(time)==='string' && time.indexOf(':')>-1){
        return time;
    }
    let s = time%60;
    let m = Math.floor(time/60);
    let h = Math.floor(time/3600);
    if(m<10) m = '0' + m;
    if(s<10) s = '0' + s;
    if(h<10) h = '0' + h;
    return h + ':' + m + ':' + s;
}

window.GetTime = function(time, type = 'yyyy-mm-dd') {
    time = new Date(time)
    let y = time.getFullYear()
    let m = time.getMonth() + 1
    let d = time.getDate()
    if (type === 'yyyy-mm-dd') {
        if (m < 10) {
            m = '0' + m
        }
        if (d < 10) {
            d = '0' + d
        }
        return y + '年' + m + '月' + d + '日';
    }
}

window.GetFullTime = function(time) {
    time = new Date(time);
    let y = time.getFullYear()
    let M =
        time.getMonth() + 1 < 10
            ? '0' + (time.getMonth() + 1)
            : time.getMonth() + 1
    let d = time.getDate() < 10 ? '0' + time.getDate() : time.getDate()
    let h = time.getHours() < 10 ? '0' + time.getHours() : time.getHours()
    let m = time.getMinutes() < 10 ? '0' + time.getMinutes() : time.getMinutes()
    let s = time.getSeconds() < 10 ? '0' + time.getSeconds() : time.getSeconds()
    let week = ["星期日","星期一","星期二","星期三","星期四","星期五","星期六"];
    let day = time.getDay();
    return y + '.' + M + '.' + d + ` ${week[day]} ` + h + ':' + m ;
}

const zero = time => {
    if (time < 10) {
        return '0' + time
    } else {
        return time
    }
}
window.GetTimeType = (dates, type) => {
    
    const date = new Date(dates)
    if (!date.getDate()) {
        console.log(new Error('传入的不是一个正确的日期对象'))
        return ''
    } else {
        if (type === 'yyyy') {
            return date.getFullYear()
        } else if (type === 'yyyy-MM') {
            return date.getFullYear() + '-' + zero(date.getMonth() + 1)
        } else if (type === 'yyyy-MM-dd') {
            return (
                date.getFullYear() +
                '-' +
                zero(date.getMonth() + 1) +
                '-' +
                zero(date.getDate())
            )
        } else if (type === 'yyyy-MM-dd hh') {
            return (
                date.getFullYear() +
                '-' +
                zero(date.getMonth() + 1) +
                '-' +
                zero(date.getDate()) +
                ' ' +
                zero(date.getHours())
            )
        } else if (type === 'yyyy-MM-dd hh:mm') {
            return (
                date.getFullYear() +
                '-' +
                zero(date.getMonth() + 1) +
                '-' +
                zero(date.getDate()) +
                ' ' +
                zero(date.getHours()) +
                ':' +
                zero(date.getMinutes())
            )
        } else if (type === 'yyyy-MM-dd hh:mm:ss') {
            return (
                date.getFullYear() +
                '-' +
                zero(date.getMonth() + 1) +
                '-' +
                zero(date.getDate()) +
                ' ' +
                zero(date.getHours()) +
                ':' +
                zero(date.getMinutes()) +
                ':' +
                zero(date.getSeconds())
            )
        }else if (type === 'yyyy.MM.dd hh:mm:ss') {
            return (
                date.getFullYear() +
                '.' +
                zero(date.getMonth() + 1) +
                '.' +
                zero(date.getDate()) +
                ' ' +
                zero(date.getHours()) +
                ':' +
                zero(date.getMinutes()) +
                ':' +
                zero(date.getSeconds())
            )
        }else if (type === 'yyyy.MM.dd hh:mm:ss') {
            return (
                date.getFullYear() +
                '.' +
                zero(date.getMonth() + 1) +
                '.' +
                zero(date.getDate()) +
                ' ' +
                zero(date.getHours()) +
                ':' +
                zero(date.getMinutes()) +
                ':' +
                zero(date.getSeconds())
            )
        } else if (type === 'hh') {
            return zero(date.getHours())
        } else if (type === 'hh:mm') {
            return zero(date.getHours()) + ':' + zero(date.getMinutes())
        } else if (type === 'hh:mm:ss') {
            return (
                zero(date.getHours()) +
                ':' +
                zero(date.getMinutes()) +
                ':' +
                zero(date.getSeconds())
            )
        } else if (type === 'MM-dd hh:mm') {
            return (
                zero(date.getMonth() + 1) +
                '-' +
                zero(date.getDate()) +
                ' ' +
                zero(date.getHours()) +
                ':' +
                zero(date.getMinutes())
            )
        } else if (type === 'yyyy_MM_dd') {
            return (
                date.getFullYear() +
                '_' +
                zero(date.getMonth() + 1) +
                '_' +
                zero(date.getDate())
            )
        } else if (type == 'yyyy.MM.dd') {
            return (
                date.getFullYear() +
                '.' +
                zero(date.getMonth() + 1) +
                '.' +
                zero(date.getDate())
            )
        } else if (type == "YYYYMMDD") {
            return (
                date.getFullYear() +
                '' +
                zero(date.getMonth() + 1) +
                '' +
                zero(date.getDate())
            )
        }
    }
}


