import Vue from 'vue'
import Router from 'vue-router'

import home from '../views/Home/index.vue'
import search from '../views/Search/index.vue'

import ExamBase from '../views/Exam/index.vue'
import ExamHome from '../views/Exam/home.vue'
import ExamInfo from '../views/Exam/info.vue'
import ExamPage from '../views/Exam/examPage.vue'
import ExamResult from '../views/Exam/result.vue'

import VoteBase from '../views/Vote/index.vue'
import VoteHome from '../views/Vote/home.vue'
import VoteDetail from '../views/Vote/detail.vue'

import SchoolBase from '../views/School/index.vue'
import SchoolHome from '../views/School/home.vue'
import SchoolDetail from '../views/School/detail.vue'

import MessageBase from '../views/Message/index.vue'
import MessageHome from '../views/Message/home.vue'
import MessageDetail from '../views/Message/detail.vue'

import ActivityBase from '../views/Activity/index.vue'
import ActivityHome from '../views/Activity/home.vue'
import ActivityDetail from '../views/Activity/detail.vue'
import ActivityEnroll from '../views/Activity/enroll.vue'
import ActivityPersonne from '../views/Activity/personne.vue'
import ActivityRegistrationFrom from '../views/Activity/RegistrationForm.vue'

import MineBase from '../views/Mine/index.vue'
import MineHome from '../views/Mine/home.vue'
import MineExam from '../views/Mine/exam.vue'
import MineVote from '../views/Mine/vote.vue'
import MineActivity from '../views/Mine/activity.vue'

import None from '../views/None/index.vue'

Vue.use(Router)

export default new Router({
    base: process.env.BASE_URL,
    routes: [
        // {
        //     path: '/',
        //     name: 'home',
        //     component: home,
        //     meta:{title:'信谊天一'},
        // },
        {
            path: '/home',
            name: 'home',
            component: home,
            meta:{title:'信谊天一'},
        },
        {
            path: '/search',
            name: 'search',
            component: search,
            meta:{title:'搜索'},
        },
        {
            path: '/exam',
            name: 'exam',
            component: ExamBase,
            redirect:'/exam/home',
            meta:{title:'考试列表'},
            children:[
                {
                    path:'home',
                    name:'home',
                    meta:{title:'考试列表'},
                    component:ExamHome,
                },
                {
                    path:'info',
                    name:'info',
                    meta:{title:'答题',type:'examination'},
                    component:ExamInfo,
                },
                {
                    path:'test',
                    name:'test',
                    meta:{title:'答题'},
                    component:ExamPage,
                },
                {
                    path:'result',
                    name:'result',
                    meta:{title:'答题结果',type:'examination'},
                    component:ExamResult,
                },
            ],
        },
        {
            path: '/vote',
            name: 'vote',
            component: VoteBase,
            meta:{title:'全部投票'},
            redirect:'/vote/home',
            children:[
                {
                    path:'home',
                    name:'home',
                    meta:{title:'全部投票'},
                    component:VoteHome,
                },
                {
                    path:'detail',
                    name:'detail',
                    meta:{title:'投票详情',type:'vote'},
                    component:VoteDetail,
                },
            ],
        },
        {
            path: '/school',
            name: 'school',
            component: SchoolBase,
            meta:{title:'内容列表'},
            redirect:'/school/home',
            children:[
                {
                    path:'home',
                    name:'home',
                    meta:{title:'内容列表'},
                    component:SchoolHome,
                },
                {
                    path:'detail',
                    name:'detail',
                    meta:{title:'内容详情',type:'school'},
                    component:SchoolDetail,
                },
            ],
        },
        {
            path: '/message',
            name: 'message',
            component: MessageBase,
            meta:{title:'内容列表'},
            redirect:'/message/home',
            children:[
                {
                    path:'home',
                    name:'home',
                    meta:{title:'内容列表'},
                    component:MessageHome,
                },
                {
                    path:'detail',
                    name:'detail',
                    meta:{title:'内容详情',type:'message'},
                    component:SchoolDetail,
                },
            ],
        },
        {
            path: '/activity',
            name: 'activity',
            component: ActivityBase,
            meta:{title:'活动列表'},
            redirect:'/activity/home',
            children:[
                {
                    path:'home',
                    name:'home',
                    meta:{title:'活动列表'},
                    component:ActivityHome,
                },
                {
                    path:'detail',
                    name:'detail',
                    meta:{title:'活动详情',type:'activity'},
                    component:ActivityDetail,
                },
                {
                    path:'enroll',
                    name:'enroll',
                    meta:{title:'报名'},
                    component:ActivityEnroll,
                },
                {
                    path:'personne',
                    name:'personne',
                    meta:{title:'邀请人员'},
                    component:ActivityPersonne,
                },
                {
                    path:'registration',
                    name:'registration',
                    meta:{title:'报名表'},
                    component:ActivityRegistrationFrom,
                },
            ],
        },
        {
            path: '/mine',
            name: 'mine',
            component: MineBase,
            redirect:'/mine/home',
            meta:{title:'我的'},
            children:[
                {
                    path:'home',
                    name:'home',
                    meta:{title:'我的'},
                    component:MineHome,
                },
                {
                    path:'exam',
                    name:'exam',
                    meta:{title:'我的考试'},
                    component:MineExam,
                },
                {
                    path:'vote',
                    name:'vote',
                    meta:{title:'我的投票'},
                    component:MineVote,
                },
                {
                    path:'activity',
                    name:'activity',
                    meta:{title:'我的报名'},
                    component:MineActivity,
                },
            ],
        },
        {
            path: '/none',
            name: 'none',
            component: None,
            meta:{title:'无权访问'},
        },
    ],
    // 页面路由刷时页面滚动到顶部
    scrollBehavior(to, from, savedPosirtion) {
        if (savedPosirtion) {
            return savedPosirtion
        } else {
            return { x: 0, y: 0 }
        }
    }
})